.page-block{
  padding: 0 0 5em;
}

.faq{
  &__grid{
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: calc(100% - 26.625em) 24.1875em;
    justify-content: space-between;
  }
}

.faq-group{
  @include lastM(0 0 1.5em);
  &__items{
    margin-top: .75em;
  }
}

.qa-card{
  &._active{
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    padding: .75em 0;
    border-bottom: 1px solid #f4f5f5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__hidden{
    display: none;
    padding: .75em .9375em;
    border-bottom: 1px solid #f4f5f5;
  }
}

.consult2{
  .container{
    padding: 0;
  }
  .h2{
    font-size: 1.875em;
    line-height: 107%;
    font-family: $font1;
  }
  .consult__subtitle{
    margin-top: .5em;
  }
  .consult-form__btn{
    background-color: $blue2;
    &:hover{
      background-color: $darkBlue;
    }
  }
  .consult-form__hint{
    a{
      color: $black;
      &:hover{
        color: $blue2;
      }
    }
  }
  @media screen and (min-width: 993px){
    .consult__inner{
      grid-template-columns: 100%;
      padding: 1.875em;
    }
    .consult-form{
      &__fields{
        grid-template-columns: 100%;
      }
    }
    .consult-form__bottom{
      display: block;
    }
    .consult-form__btn{
      margin: 0 0 .75em;
      width: 100%;
    }
    .socials{
      margin-top: 1.25em;
    }
    .consult__img{
      width: 12.5em;
      right: -5em;
      top: -2.75em;
      bottom: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .page-block{
    padding: 0 0 2.5em;
  }

  .faq{
    &__grid{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    &__grid{
      margin-top: 1.25em;
    }
  }
}
