.about{
  &__inner{
    display: grid;
    grid-template-columns: 41.5em calc(100% - 44.5em);
    gap: 3em;
  }
  &__col2{
    align-self: center;
  }
  .text{
    color: #424244;
    margin-top: 1.5em;
    p{
      line-height: 153%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.75em;
  }
  &__btn{
    height: 3.875em;
    padding: 0 3.125em;
    margin-right: 1.25em;
    img{
      width: 1.375em;
      margin-left: .75em;
    }
  }
}

.map{
  border-radius: .75em;
  overflow: hidden;
  height: 31em;
  .ymaps-2-1-79-map{
    mix-blend-mode: exclusion;
    background-blend-mode: exclusion, hard-light;
  }
}

.shop{
  &__inner{
    display: grid;
    grid-template-columns: 40.375em calc(100% - 43.375em);
    gap: 3em;
  }
  .h2{
    max-width: 11em;
  }
  .text{
    color: #424244;
    margin-top: 1.5em;
    ul{
      gap: 1.25em;
      li:before{
        background-image: url(../img/check2.svg);
      }
    }
  }
  &__nav{
    display: none;
  }
}

.shop-slider{
  .swiper{
    &-nav{
      position: absolute;
      bottom: 1.875em;
      left: 1.875em;
      z-index: 3;
      display: flex;
    }
    &-button{
      position: static;
      transform: unset;
      margin: 0 .625em 0 0;
    }
  }
}

.shop-info{
  position: relative;
  &__img{
    @include imgCover(74.7%);
    border-radius: .75em;
    &:after{
      background: rgba(37, 47, 65, 0.4);
    }
  }
  &__content{
    position: absolute;
    bottom: 6.25em;
    left: 1.875em;
    width: 19.375em;
    max-width: 100%;
    color: #fff;
    @include grid100gap(1em);
  }
  &__desc{
    line-height: 144%;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__inner{
      grid-template-columns: 100%;
      gap: .875em;
    }
    .text{
      margin-top: .75em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      padding: 0 1.875em;
      height: 2.875em;
    }
  }

  .map{
    height: 15.5em;
  }

  .shop{
    &__inner{
      grid-template-columns: 100%;
      gap: .875em;
    }
    .text{
      margin-top: .75em;
      ul{
        gap: .75em;
      }
    }
  }

  .shop-slider{
    .swiper{
      &-nav{
        top: unset;
        bottom: 1.125em;
      }
      &-button{
        width: 2.125em;
        height: 2.125em;
      }
    }
  }

  .shop-info{
    &__img{
      padding-bottom: 45.8%;
    }
    &__content{
      bottom: 3.5em;
      left: 1.75em;
      gap: .25em;
    }
    &__title{
      max-width: 8.3em;
      line-height: 104%;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    margin: 0;
    padding-top: 0;
    .text{
      p{
        font-size: .9375em;
      }
    }
    &__nav{
      margin-top: .75em;
    }
  }

  .shop{
    &__nav{
      justify-content: center;
      margin-top: 1em;
    }
    &__btn{
      display: flex;
      align-items: center;
      font-weight: 500;
      color: $blue2;
      span{
        border-bottom: 1px dashed rgba(15, 108, 218, 0.5);
        font-size: 1em;
        line-height: 137%;
      }
      img{
        width: .5625em;
        margin-left: .375em;
      }
    }
  }

  .shop-slider{
    .swiper-nav{
      left: .9375em;
      bottom: 1em;
      right: unset;
      transform: unset;
    }
  }

  .shop-info{
    &__img{
      padding-bottom: 74.8%;
    }
    &__content{
      left: .9375em;
      bottom: 4em;
    }
  }

  .about-detail{
    &._show{
      .shop .shop__nav{
        display: none;
      }
      .about,
      .shop__col2{
        display: block;
      }
    }
    .shop__nav{
      display: flex;
    }
    .about,
    .shop__col2{
      display: none;
    }
  }
}
