.favourite-page{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__select{
    display: none;
  }
}

.delete-btn{
  display: flex;
  align-items: center;
  color: $grayText;
  &:hover{
    color: $red;
    path{
      stroke: $red;
    }
    span{
      border-color: $red;
    }
  }
  svg{
    width: 1.5em;
    margin-right: .625em;
  }
  span{
    font-size: .9375em;
    line-height: 140%;
    border-bottom: 1px dotted $grayText;
  }
}

.favourite-nav{
  padding: 1.5em 1.25em;
  border: 1px solid #eff1f4;
  border-radius: .25em;
  &__nav{
    @include grid100gap(.75em);
    margin-top: 1.25em;
    padding-left: 1.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &._active{
      color: $blue2;
      .favourite-nav__link-title{
        font-weight: 700;
      }
    }
    &-title{
      font-size: 1em;
      line-height: 144%;
    }
    &-count{
      font-size: .6875em;
      color: $p;
    }
  }
}

.favourite-empty{
  margin-top: 1.875em;
  display: flex;
  align-items: center;
  &__icon{
    margin-right: 1.875em;
    width: 6.5em;
    height: 6.5em;
    flex-shrink: 0;
    @include center;
    position: relative;
    padding: .625em .125em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
    &._type2{
      padding: 0;
      width: 7.25em;
      height: 7.25em;
      .pin{
        right: -.625em;
      }
    }
    &._type3{
      padding: 0;
      width: 9.0625em;
      height: 7.5em;
    }
  }
  .pin{
    position: absolute;
    right: 0;
    top: 0;
  }
  &__content{
    max-width: 40em;
    @include grid100gap(.75em);
  }
  &__desc{
    font-size: 1.0625em;
    line-height: 135%;
    color: $grayText;
    a{
      font-weight: 700;
    }
  }
  &__link{
    margin-left: auto;
  }
}

.pin{
  height: 1.75em;
  min-width: 1.75em;
  padding: 0 .25em;
  background-color: $sh;
  border-radius: .625em;
  color: #fff;
  font-weight: 700;
  @include center;
  span{
    font-size: 1em;
  }
}


@media screen and (max-width: 992px){
  .favourite-page{
    &__top{
      display: block;
    }
    &__nav{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 1.875em;
    }
    &__select{
      display: block;
      min-width: 16.5em;
    }
  }

  .favourite-nav{
    display: none;
  }

  .favourite-empty{
    flex-direction: column;
    text-align: center;
    &__icon{
      margin: 0 0 .75em;
      width: 4.125em;
      height: 4.125em;
      padding: .625em .25em;
      &._type2{
        width: 4.375em;
        height: 4.375em;
        .pin{
          top: .375em;
          right: -1em;
        }
      }
      &._type3{
        width: 6em;
        height: 5em;
      }
    }
    .pin{
      right: -.25em;
      top: -.25em;
    }
    &__content{
      max-width: 26em;
    }
    &__title{
      font-size: 1.1875em;
    }
    &__link{
      margin: 1em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .favourite-page{
    &__nav{
      flex-direction: column;
      margin: .5em 0 0;
      align-items: flex-start;
    }
    &__select{
      order: 2;
      margin: 1.5em 0 0;
      width: 100%;
    }
  }

  .favourite-empty{
    margin-top: .5em;
  }
}
