.about-main{
  padding-bottom: 5em;
  &__inner{
    position: relative;
    min-height: 30.25em;
    padding-right: 32.875em;
  }
  &__media{
    position: absolute;
    width: 31em;
    right: 0;
    top: 0;
  }
  &__img{
    position: relative;
    z-index: 2;
    width: 100%;
    filter: drop-shadow(15px 8px 39px rgba(46, 51, 62, 0.17))
            drop-shadow(62px 33px 70px rgba(46, 51, 62, 0.14))
            drop-shadow(139px 75px 95px rgba(46, 51, 62, 0.09))
            drop-shadow(247px 133px 112px rgba(46, 51, 62, 0.03))
            drop-shadow(386px 207px 123px rgba(46, 51, 62, 0));
  }
  &__img2{
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
  }
  .text{
    margin-top: 1.875em;
    p{
      line-height: 153%;
    }
  }
  .shop__btn{
    display: none;
  }
}

.why{
  padding: 5em 0;
  background-color: $gray;
  border-top: $border;
  border-bottom: $border;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em));
    gap: 2.5em;
    margin-top: 1.875em;
  }
}

.why-item{
  display: flex;
  &__icon{
    width: 2.8125em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: .25em;
  }
  &__title{
    font-weight: 500;
    font-size: 1.25em;
    line-height: 130%;
  }
  &__desc{
    font-size: 1em;
    line-height: 144%;
    color: $p;
    margin-top: .375em;
  }
}

.contacts{
  padding-bottom: 2.5em;
  .map{
    margin-top: 2.5em;
    width: 100%;
    overflow: unset;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1em));
    gap: 1.25em;
  }
}

.contact-item{
  padding: 1.25em;
  border: 1px solid $gray;
  border-radius: .3125em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 600;
    margin-bottom: 1em;
  }
  &__desc{
    display: flex;
    align-items: flex-start;
    img{
      margin-right: .5em;
      width: 1em;
    }
    span{
      margin-top: -.3125em;
      font-size: 1em;
      line-height: 144%;
    }
  }
}

.contact-socials{
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    @include center;
    width: 1.125em;
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
    img{
      width: 100%;
    }
  }
  &__hint{
    margin-top: .5em;
    color: $p;
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding-bottom: 2.5em;
    &__inner{
      padding: 0;
      min-height: unset;
    }
    &__media{
      position: relative;
      float: right;
      width: 20.25em;
    }
    .text{
      margin-top: .5em;
      p{
        line-height: 144%;
      }
    }
  }

  .why{
    padding: 2.5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
      gap: 1.125em 1.5625em;
      margin-top: 1.25em;
    }
  }

  .contacts{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    .map{
      height: 20.5em;
    }
  }

  .contact-item{
    &--full{
      grid-column: 1/-1;
    }
    &__desc{
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    padding-bottom: 0;
    &:not(._show){
      .text{
        @include truncate(10);
        p{
          display: inline;
          margin: 0 !important;
        }
      }
    }
    &._show{
      .shop__btn{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &__inner{
      display: flex;
      flex-direction: column;
    }
    &__media{
      width: 100%;
      order: 3;
      margin-top: .75em;
    }
    .shop__btn{
      display: flex;
      ._view2{
        display: none;
      }
    }
  }

  .why{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .contacts{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
