.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.font1{
  font-family: $font1;
}

.font2{
  font-family: $font2;
}

.font3{
  font-family: $font3;
}

.blue-text{
  color: $blue;
}

.blue-text2{
  color: $blue2;
}

.green-text{
  color: $green;
}

.gray-text{
  color: $grayText;
}

.red-text{
  color: $red;
}

.p-text{
  color: $p;
}

.blue-link{
  color: $blue2;
  &:hover{
    color: $blue;
  }
}

.sh-text{
  color: $sh;
}



