.lk{
  &__grid{
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: 20.5em calc(100% - 20.5em - 1.875em);
    gap: 1.875em;
  }
}

.menu-btn{
  display: none;
}

.lk-menu{
  position: sticky;
  top: 1em;
  &__bg,
  &__close{
    display: none;
  }
}

.lk-nav{
  overflow: hidden;
  background: #FFF;
  border: $border2;
  border-radius: .3125em;
}

.lk-link{
  min-height: 4.625em;
  display: flex;
  align-items: center;
  padding: .75em 1.875em;
  color: #232323;
  border-bottom: $border2;
  &:last-child{
    border-bottom: none;
  }
  &._active,
  &:hover{
    border-color: $blue2;
    background-color: $blue2;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  &__icon{
    width: 1.4375em;
    flex-shrink: 0;
    margin-right: .625em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
  }
  &__count{
    position: static;
    margin-left: auto;
    transform: unset;
    border: none;
    height: 1em;
    border-radius: .375em;
    font-weight: 500;
    span{
      font-size: .75em;
    }
  }
}

.lk-buttons{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
  gap: 1.875em;
}

.lk-btn{
  min-height: 9.375em;
  position: relative;
  padding: 1.25em 1em;
  border: $border2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: .3125em;
  .lk-link__count{
    position: absolute;
    top: -.625em;
    right: -.625em;
  }
  &__icon{
    height: 4em;
    flex-shrink: 0;
    @include center;
    svg{
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 144%;
  }
  &:hover,
  &._active{
    background-color: $blue2;
    border-color: $blue2;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
}

._hide-menu-on-desktop{
  @media screen and (min-width: 993px){
    .lk-buttons{
      display: none;
    }
  }
}

.profile-form,
.change-pass{
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 3.5625em;
  }
}

.profile-form{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
  margin-top: 1.875em;
  &__type{
    grid-column: 1/-1;
    display: flex;
    align-items: center;
    &-items{
      display: flex;
      align-items: center;
    }
    .m-radio{
      margin-left: 1.125em;
    }
  }
  &:not(._co){
    ._hide-on-personal{
      display: none;
    }
  }
}

.change-pass{
  margin-top: 1.875em;
  @include grid100gap(1.25em);
  width: 27.75em;
  max-width: 100%;
}

.success-mess{
  display: flex;
  align-items: center;
  color: #07961E;
  img{
    width: 1em;
    margin-right: .5em;
  }
}


@media screen and (max-width: 992px){
  .lk{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col{
      position: relative;
      z-index: 3;
    }
  }

  .lk-menu{
    @include menu;
    &._open{
      transform: translateX(0);
      &~.lk-menu__bg,
      &~.lk-menu__close{
        display: block;
      }
    }
  }

  .lk-nav{
    border-radius: 0;
    border: none;
  }

  .lk-buttons{
    grid-template-columns: repeat(5,calc(20% - 3.75em / 5));
    gap: .9375em;
    margin-bottom: 1.75em;
  }

  .lk-btn{
    justify-content: unset;
    min-height: 7.125em;
    &__icon{
      height: 2.4375em;
      margin-bottom: .5em;
    }
    &__title{
      font-size: .875em;
      line-height: 100%;
    }
  }

  .profile-edit{
    margin-top: 1.75em;
  }

  .profile-form{
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .menu-btn{
    @include center;
    height: 3.125em;
    border: $border;
    width: 100%;
    font-weight: 500;
    margin-bottom: 1.75em;
    span{
      font-size: 1.0625em;
    }
    svg{
      width: .8125em;
      margin-right: .625em;
    }
  }

  .lk-buttons{
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
  }

  .lk-btn{
    min-height: 7.75em;
    &__icon{
      margin-bottom: .25em;
    }
    &__title{
      font-size: 1em;
      line-height: 144%;
    }
  }

  .profile-form{
    grid-template-columns: 100%;
    &__type{
      display: block;
      &-items{
        margin-top: 1em;
      }
      .m-radio{
        margin-left: 0;
        @include lastM(0 .5em 0 0);
      }
    }
  }

  ._hide-menu-on-mob{
    .lk-buttons{
      display: none;
    }
  }
}
