.auth{
  &__inner{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 32.8125em 45.9375em;
    justify-content: space-between;
    align-items: center;
    gap: 1.56em;
    &._type2{
      grid-template-columns: 43.375em 36em;
    }
  }
  .info-message{
    margin-top: 2.5em;
  }
  &__col2{
    align-self: flex-start;
  }
}

.m-form,
.reg-form,
.login-form{
  padding: 3.125em;
  background-color: $gray;
}

.login-form{
  @include grid100gap(1.56em);
  &__remember{
    display: inline-block;
    margin: 0 auto;
  }
  &__hint{
    text-align: center;
  }
  &._type2{
    padding: 2.5em;
    border-radius: 0.1875em;
    border: $border2;
    margin: 2.5em auto 0;
    width: 34em;
    max-width: 100%;
  }
}

.m-link{
  @include center;
  height: 2em;
  border-radius: 0.25em;
  //background-color: $lightGray;
  span{
    font-size: 1em;
    line-height: 140%;
  }
}

.login-info{
  height: 100%;
  &._type2{
    padding-top: 5em;
    align-self: flex-start;
    .login-info__icon{
      width: 2.8125em;
    }
  }
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    min-height: 2.9375em;
    display: flex;
    align-items: center;
    font-weight: 800;
  }
  .text{
    margin-top: 1.25em;
    color: #363a3a;
  }
  &__nav{
    display: flex;
    margin-top: 1.56em;
    .m-btn{
      padding: 0 2.1875em;
    }
  }
}

.reg{
  .auth__inner{
    align-items: flex-start;
  }
  .login-info{
    &__nav{
      margin-top: .625em;
    }
  }
}

.info-message{
  padding: .625em;
  display: flex;
  align-items: center;
  border-radius: 0.1875em;
  border: 1px dashed #7EACB8;
  background: #F6FBFC;
  color: $blue;
  &._center{
    justify-content: center;
  }
  &._big{
    @media screen and (min-width: 993px){
      padding: 1.81em;
    }
  }
  a{
    text-decoration: underline;
  }
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .625em;
  }
}

.reg-form{
  @include grid100gap(2.5em);
  &:not(._co){
    ._hide-on-personal{
      display: none;
    }
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__title{
    margin-bottom: .5em;
  }
  &__variant{
    display: none;
    &._active{
      display: block;
    }
  }
  &__radios{
    display: flex;
    margin-top: .875em;
    .m-radio{
      @include lastM(0 1.875em 0 0);
      ._view2{
        display: none;
      }
    }
  }
}

.pass-form{
  margin: 1.875em auto 0;
  width: 32.8125em;
  max-width: 100%;
  @include grid100gap(1.56em);
  &__desc{
    text-align: center;
  }
  &__icon{
    width: 4.125em;
    margin: 0 auto;
  }
}


@media screen and (max-width: 992px){
  .auth{
    &__inner{
      margin-top: 1.56em;
      grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
      &._type2{
        grid-template-columns: 26.125em calc(100% - 26.125em - 1.5625em);
      }
    }
    .info-message{
      margin-top: 1.25em;
    }
  }

  .login-form{
    padding: 1.5em;
    &._type2{
      margin-top: 1.56em;
      padding: 1.56em;
      width: 32.125em;
    }
  }

  .login-info{
    position: relative;
    text-align: center;
    &._type2{
      padding-top: 2.5em;
      .login-info__icon{
        margin-right: 1em;
      }
    }
    &__top{
      justify-content: center;
    }
    .text{
      margin-top: .75em;
      p{
        display: inline;
      }
    }
    &__nav{
      margin-top: .75em;
      justify-content: center;
    }
  }

  .info-message{
    padding: .9375em .625em;
  }

  .reg-form{
    padding: 1.5em;
    &__title{
      font-size: 1.0625em;
    }
    &__fields{
      grid-template-columns: 100%;
    }
    &__radios{
      .m-radio{
        @include lastM(0 1.25em 0 0);
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .pass-form{
    margin-top: 1.25em;
    &._type2{
      padding: 1.5em;
      gap: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .auth{
    &__inner{
      grid-template-columns: 100%;
      &._type2{
        grid-template-columns: 100%;
      }
    }
  }

  .login-form{
    gap: .9375em;
  }

  .pass-form{
    padding: 1.5em;
    gap: .75em;
  }
}
