.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 2.25em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
    margin-top: -.625em;
  }
  .range-slider{
    width: 100%;
    background-color: $comm;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $black;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $black;
      width: 1em;
      height: 1em;
      top: -.4375em !important;
      right: -.4375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.25em;
  }
  .fg{
    position: relative;
    input{
      height: 1.86em;
      font-family: $font1;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      line-height: 140%;
      padding: 0 .57em;
      border-radius: .57em;
    }
    &__title{
      font-size: .875em;
      display: block;
      color: $p;
      margin-bottom: .25em;
    }
    &__desc{
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: .875em;
      line-height: 140%;
      color: $p;
      height: 1.86em;
      display: flex;
      align-items: center;
      padding-right: .57em;
      &~input{
        padding-right: 2em;
      }
    }
  }
}
