.header-menu{
  &__title,
  &__back,
  &__bg,
  &__close{
    display: none;
  }
  @media screen and (min-width: 993px){
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    box-shadow: 0 20px 120px 0 rgba(166, 179, 212, 0.29);
    background-color: #fff;
    margin-top: 1.25em;
    border-radius: 0 0 .75em .75em;
    display: grid;
    grid-template-columns: 16em calc(100% - 16em);
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.catalog-menu2{
  min-height: 38em;
  &__show{
    display: flex;
    align-items: center;
    padding: 0 1.875em;
    min-height: 4.4375em;
    font-weight: 500;
    font-size: 1em;
    line-height: 125%;
    transition: all .3s;
    background-color: $gray;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 1em;
      transform: translateX(-50%);
    }
    &:hover{
      cursor: pointer;
      background-color: #fff;
      color: $blue2;
    }
  }
  &__item{
    @media screen and (min-width: 993px){
      &._active{
        .catalog-menu__show{
          background-color: #fff;
          color: $blue2;
        }
        .catalog-submenu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.catalog-submenu{
  position: absolute;
  width: calc(100% - 16em);
  height: 100%;
  right: 0;
  top: 0;
  padding: 2.5em;
  column-count: 4;
  column-gap: 2.5em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__item{
    margin-bottom: 1.25em;
    width: 100%;
    display: inline-block;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__show{
    display: inline-flex;
    width: 100%;
  }
  &__icon{
    width: 2.5em;
    height: 2.5em;
    border-radius: .25em;
    background-color: $gray;
    @include center;
    margin-right: 1em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__title{
    min-height: 2.5em;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 1em;
    line-height: 125%;
    a{
      line-height: inherit;
    }
  }
  &__hidden{
    padding: .25em 0 0 3.5em;
  }
  &__links{
    @include grid100gap(.5em);
    color: $p;
    a{
      font-size: 1em;
      line-height: 125%;
    }
  }
}

.menu-contacts,
.nav3{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &._open{
      .header-menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
        &__close{
          display: flex;
        }
      }
    }
  }

  .header-menu{
    display: block;
    @include menu;
    &._open-submenu{
      overflow: hidden;
      .header-menu__back{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__back{
      opacity: 0;
      pointer-events: none;
      padding: 0 1.25em;
      display: flex;
      align-items: center;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 3.75em;
      background-color: $gray;
      z-index: 5;
      font-weight: 700;
      img{
        width: 1.25em;
        margin-right: .625em;
      }
    }
    &__title{
      display: flex;
      align-items: center;
      padding: 0 1.25em;
      background-color: $gray;
      height: 3.75em;
      font-weight: 700;
      img{
        width: 1.25em;
        margin-right: .625em;
      }
    }
  }

  .menu-contacts{
    padding: 1.25em 3.125em;
    border-top: $border;
    border-bottom: $border;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: $gray;
    .contact-socials__items{
      margin-bottom: .25em;
    }
    .header-contacts__item{
      border: none !important;
      padding: 0 !important;
      margin-right: 0!important;
      margin-top: 1em !important;
    }
  }

  .nav3{
    padding: 1em 3.125em;
    display: block;
    border-top: $border;
    color: $p;
    .menu{
      @include grid100gap(1em);
    }
    a{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .catalog-menu2{
    min-height: unset;
    &__item{
      border-bottom: $border;
      //&._active{
      //  opacity: 1;
      //  pointer-events: unset;
      //}
      &._active2{
        .catalog-submenu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__show{
      min-height: 3.1875em;
      padding: 0 1.25em 0 3.125em;
      justify-content: space-between;
      background-color: transparent;
      &:after{
        position: static;
        background-image: url(../img/arrow5.svg);
        transform: unset;
        width: .3125em;
        height: .625em;
        @include bgImgSize(100%);
      }
    }
  }

  .catalog-submenu{
    position: absolute;
    bottom: 0;
    left: 0;
    top: unset;
    right: unset;
    width: 100%;
    height: calc(100 * var(--vh) - 3.75em);
    background-color: #fff;
    column-count: 1;
    overflow-y: auto;
    z-index: 20;
    padding: 1.25em 1.25em 1.25em 3.125em;
    display: flex;
    flex-direction: column;
    &__item{
      width: 100%;
      margin-bottom: 1.25em;
      &._active{
        .catalog-submenu__show:after{
          transform: rotate(-180deg);
        }
      }
    }
    ._has-children{
      .catalog-submenu__show{
        &:after{
          content: '';
          flex-shrink: 0;
          margin-left: auto;
          width: .5625em;
          height: .5625em;
          background-image: url(../img/arrow6.svg);
          @include bgImgSize(100%);
          align-self: center;
        }
      }
    }
    &__hidden{
      display: none;
    }
  }
}


