.blog-page{
  padding-bottom: 5em;
  .blog-card{
    &__img{
      border-radius: .75em;
    }
    &__content{
      padding: 0;
      margin-top: .75em;
    }
    &__nav{
      padding-top: .75em;
    }
  }
}

.blog-item{
  .text{
    margin-top: 2.5em;
  }
  &__date{
    font-size: 1em;
    line-height: 144%;
    color: $p;
    margin-top: .25em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: .3125em;
    margin-top: 2.375em;
  }
  &__info{
    margin-top: 2.5em;
    padding: 1.75em 1.875em;
    border-radius: .75em;
    background-color: $gray;
    color: $darkBlue;
    @include grid100gap(1em);
    p{
      font-size: 1.125em;
      line-height: 128%;
    }
  }
}

.article-slider{
  margin-bottom: 1.5em;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
    &-button{
      @media screen and (min-width: 577px){
        &.swiper-button-disabled{
          opacity: 0;
        }
      }
    }
  }
  &__img{
    border-radius: .3125em;
    @include imgCover(60%);
  }
}

.article-video{
  width: 54.125em;
  max-width: 100%;
  display: block;
  margin: 1.25em auto;
  position: relative;
  color: #fff;
  &__play{
    margin-top: 1em;
    @include centerAbsolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-icon{
      width: 5em;
      height: 5em;
      background-color: $blue;
      border-radius: 50%;
      @include center;
      img{
        width: 1.5625em;
      }
    }
    &-text{
      font-size: 1.0625em;
      line-height: 135%;
      text-align: center;
      margin-top: .5em;
    }
  }
  &__img{
    @include imgCover(56.2%);
    border-radius: .625em;
    &:after{
      background: rgba(2, 38, 80, 0.28);
    }
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 1.25em 1.5em;
  }
}


@media screen and (max-width: 992px){
  .blog-page{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
      gap: 1.525em;
    }
    .blog-card {
      &__img {
        border-radius: .3125em;
      }
    }
  }

  .blog-item{
    &__date{
      font-size: .875em;
    }
    &__img{
      margin-top: 1em;
      object-fit: cover;
      min-height: 22.5em;
    }
    &__info{
      margin-top: 1.25em;
      padding: .75em .9375em;
      gap: 0;
      p{
        font-size: 1em;
        line-height: 144%;
      }
    }
  }

  .article-slider{
    margin-bottom: 1em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
    }
    &__img{
      padding-bottom: 70%;
    }
  }

  .article-video{
    &__play{
      &-icon{
        width: 4.625em;
        height: 4.625em;
        img{
          width: 1.4375em;
        }
      }
      &-text{
        display: none;
      }
    }
    &__top{
      padding: .75em 1em;
    }
    &__title{
      font-size: 1.0625em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-page{
    padding-bottom: 2.5em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .blog-item{
    &__img{
      min-height: 12.75em;
    }
  }

  .article-slider{
    padding-bottom: 3.75em;
    .swiper{
      &-slide{
        width: 100%;
      }
      &-nav{
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        display: flex;
      }
      &-button{
        position: static;
        transform: unset;
        margin: 0 .3125em;
      }
    }
  }

  .article-video{
    &__play{
      &-icon{
        width: 3.125em;
        height: 3.125em;
        img{
          width: 1em;
        }
      }
    }
  }
}
