@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/_filter-slider.scss";
.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: .25em;
    border: 1px solid $comm;
    @include lastM(0 .625em 0 0);
    @include center;
    &:hover{
      border-color: $blue2;
      background-color: rgba($blue2,.1);
    }
    img{
      width: 1.5em;
    }
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  width: 1.25em;
  height: 1.25em;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  &:hover{
    path{
      opacity: 1;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $red;
      fill: $red;
    }
  }
}

.compare-btn{
  &:hover{
    path{
      opacity: 1;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $blue2;
    }
  }
}
