.pagination{
  margin-top: 1.875em;
  display: flex;
  li{
    margin-right: .625em;
    &:hover,
    &.active{
      a{
        background-color: $blue2;
        border-color: $blue2;
        color: #fff;
      }
    }
    &.prev,
    &.next{
      a{
        border-color: $darkBlue;
        background-color: $darkBlue;
        padding: 0 1.25em;
        color: #fff;
        &:hover{
          background-color: $blue2;
          border-color: $blue2;
        }
        &:after{
          content: '';
          background-image: url(../img/next.svg);
          width:  .9375em;
          height: .9375em;
          @include bgImgSize(100%);
          margin-left: .75em;
        }
      }
    }
    &.prev{
      margin-right: .9375em;
      a{
        &:after{
          @media screen and (min-width: 576px){
            order: -1;
            margin: 0 .75em 0 0;
            transform: rotate(180deg);
          }
        }
      }
    }
    &.next{
      margin-left: .3125em;
    }
  }
  a{
    font-size: 1em;
    padding: 0 .9375em;
    @include center;
    font-weight: 500;
    border: 1px solid $comm;
    border-radius: .25em;
    height: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 1.5em;
    li{
      &.next{
        margin-left: 0;
      }
      &.prev{
        margin-right: .625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .pagination{
    li{
      &.next,
      &.prev{
        a{
          width: 2.5em;
          overflow: hidden;
          color: transparent;
          position: relative;
          &:after{
            margin: 0;
            @include centerAbsolute;
          }
        }
      }
      &.prev{
        a{
          transform: rotate(-180deg);
        }
      }
    }
  }
}
