.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $red;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.25em;
    border: $border;
    padding: 0 1.25em;
    height: 2.75em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: $black;
    &::placeholder {
      color: $p;
    }
    &:focus {
      border-color: $blue2;
    }
  }
  textarea{
    padding-top: .875em;
    padding-bottom: .875em;
    height: 5.25em;
    resize: none;
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    right: 1.25em;
    &~input{
      padding-right: 2.75em;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.0625em;
    padding: 0 0 0 1.25em;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &:focus{
      border-color: $borderColor;
    }
    &.open{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:before{
      content: '';
      order: 3;
      border-left: $border;
      flex-shrink: 0;
      width: 2.5em;
      height: 100%;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(.5em);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -1px;
      border-radius: 0 0 .1875em .1875em;
      width: calc(100% + 2px);
      left: -1px;
      border: $border;
      background: #FFF;
      box-shadow: none;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 2.5em;
      font-size: 1em;
      line-height: 144%;
      padding: .5em 1.25em;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      border-bottom: $border;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $blue;
        border-color: $blue;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &--big{
    input{
      height: 3.0625em;
    }
  }
  &._error{
    input,
    textarea{
      border-color: $red;
      color: $red;
      &::placeholder{
        color: $red;
      }
    }
    .fg__error-text{
      display: flex;
    }
  }
  &__error-text{
    margin-top: .125em;
    color: $red;
    display: none;
    img{
      width: 1em;
      margin-right: .25em;
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.time-radio,
.m-switch,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.625em;
    min-height: 1.125em;
    padding-top: .0625em;
    span{
      font-size: 1em;
      line-height: 112.5%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: $border3;
      width: 1.125em;
      height: 1.125em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._big{
    label{
      padding-top: 0;
      span{
        font-size: 1em;
        line-height: 140%;
      }
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $blue2;
        border-color: $blue2;
        background-size: .625em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check3.svg);
    }
  }
  &._type2{
    label{
      color: $p;
      display: flex;
      align-items: center;
      &:before{
        top: 50%;
        transform: translateY(-50%);
      }
    }
    span{
      font-size: .875em;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
}

.m-switch{
  display: flex;
  label{
    width: 2.5625em;
    height: 1.5625em;
    border-radius: 2em;
    background-color: $comm;
    display: flex;
    align-items: center;
    padding: 0 .1875em;
    transition: all .3s;
    &:before{
      content: '';
      transition: inherit;
      width: 1em;
      height: 1em;
      border-radius: 50%;
      background-color: $white;
    }
  }
  input:checked~label{
    background-color: $blue2;
    &:before{
      transform: translateX(1.1875em);
    }
  }
}

.cartcalc{
  flex-shrink: 0;
  max-width: 100%;
  width: 5rem;
  display: grid;
  grid-template-columns: 1.375em calc(100% - 1.375em * 2) 1.375em;
  height: 2.5625em;
  border-radius: .25em;
  background-color: $gray;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
    border-radius: inherit;
  }
  input{
    border: none;
    color: $black;
    background-color: transparent;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .625em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #e7e9ee;
      path{
        stroke: $blue2;
      }
    }
    &._disabled{
      pointer-events: none;
      svg{
        opacity: .2;
      }
    }
  }
}


