@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3.125em;
    font-weight: 500;
    border-radius: .25em;
    height: 3.125em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-big{
    height: 3.6875em;
}

.m-btn-medium{
    height: 2.5625em;
    padding: 0 1.5em;
}

.m-btn-small{
    height: 2.3125em;
    padding: 0 1.5625em;
}

.m-btn-blue-outline{
    border: 1px solid $blue2;
    color: $blue2;
    &:hover{
        background-color: $blue;
        border-color: $blue;
        color: $white;
    }
}

.m-btn-dark-outline{
    border: .125em solid $darkBlue;
    color: $darkBlue;
    &:hover{
        background-color: $darkBlue;
        color: $white;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: $blue2;
        color: $white;
        ._stroke{
            stroke: $white;
        }
    }
}

.m-btn-black,
.m-btn-dark{
    background-color: $darkBlue;
    color: $white;
    &:hover{
        background-color: $blue2;
        color: $white;
    }
}

.m-btn-black{
    background-color: #151b30;
    &:disabled{
        background-color: rgba(#151b30,.3);
    }
}

.m-btn-blue{
    background-color: $blue2;
    color: $white;
    &:hover{
        background-color: $darkBlue;
        color: $white;
    }
}

.m-btn-round{
    width: 3.125em;
    height: 3.125em;
    padding: 0;
    background-color: $white;
    border-radius: 50%;
    box-shadow: 4px 6px 20px 0 rgba(82, 103, 157, 0.08);
    &:hover{
        background-color: $blue2;
        path{
            stroke: $white;
        }
    }
    svg{
        width: 1.25em;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.5625em;
        padding: 0 2.5em;
        &-normal{
            height: 3.125em;
            padding: 0 3.125em;
        }
    }

    .m-btn-round{
        width: 2.25em;
        height: 2.25em;
        padding: 0;
        svg{
            width: .75em;
        }
    }

    .m-btn-medium{
        padding: 0 1.5em;
    }

    .m-btn-small{
        height: 2.3125em;
        padding: 0 1.5625em;
    }
}


