.main{
  padding: 2.5em 0 5em;
  background-color: $gray;
  &__items{
    margin-top: 2.5em;
    grid-auto-flow: dense;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1.875em;
    }
    &-button{
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
      &-prev{
        left: -1.5625em;
      }
      &-next{
        right: -1.5625em;
      }
    }
  }
}

.swiper-button{
  width: 3.125em;
  height: 3.125em;
  border-radius: 50%;
  @include center;
  background-color: $white;
  box-shadow: 0 2px 6px 0 rgba(50, 75, 140, 0.07);
  svg{
    width: 1em;
  }
  &:hover{
    background-color: $blue2;
    path{
      stroke: $white;
    }
  }
}

.main-banner{
  padding: 3.75em;
  background: radial-gradient(208.99% 274.68% at 123.9% 187.5%, rgb(99, 129, 189) 0%, rgb(23, 35, 60) 100%);
  border-radius: .75em;
  border: $border;
  color: $white;
  height: 100%;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    right: -4.5em;
    height: 100%;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    width: 43.75em;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 1.875em;
    max-width: 25em;
  }
  .m-badge{
    margin-bottom: 1.25em;
  }
  &__nav{
    display: flex;
    margin-top: 2.1875em;
  }
  &__sale{
    top: -1.125em;
    right: -5em;
    position: absolute;
    width: 7.4375em;
    filter: drop-shadow(1px 3px 6px rgba(0, 0, 0, 0.1))
           drop-shadow(4px 10px 11px rgba(0, 0, 0, 0.09))
           drop-shadow(10px 23px 15px rgba(0, 0, 0, 0.05))
           drop-shadow(17px 41px 18px rgba(0, 0, 0, 0.01))
           drop-shadow(27px 64px 19px rgba(0, 0, 0, 0));
  }
}

.m-badge{
  display: flex;
  align-items: center;
  padding: 0 .5em;
  height: 1.25em;
  background: #f62759;
  border-radius: .25em;
  color: $white;
  font-weight: 500;
  &._yellow{
    background: #f6b819;
  }
  &._green{
    background: #08a56c;
  }
  &._blue{
    background: #0061aa;
  }
  &._orange{
    background: #ff7043;
  }
}

.main-item,
.main-item2{
  border-radius: .75em;
  color: $white;
  min-height: 17.875em;
  position: relative;
  overflow: hidden;
  &__content{
    position: relative;
    z-index: 2;
  }
  &__desc{
    max-width: 18.125em;
    margin-top: .5em;
  }
}

.main-item{
  background: linear-gradient(126deg, #015bc6 0%, #73cdff 100%);
  padding: 3em 2.5em;
  grid-column: span 2;
  &._type2{
    background: radial-gradient(217.72% 211.57% at 28.999999999999996% -38%,rgb(64, 72, 99),rgb(64, 72, 99) 9.513%,rgb(21, 27, 48) 84.5%);
  }
  &__img{
    pointer-events: none;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    img{
      height: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__link2{
    z-index: 2;
    position: absolute;
    right: 1.25em;
    bottom: 1.25em;
  }
}

.main-item2{
  background-image: radial-gradient(250.59% 129.44% at 83.86% 111.54%, rgb(158, 179, 217) 0%, rgb(117, 134, 165) 100%);
  background-color: #9ca9c1;
  padding: 2.25em 2.5em;
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.5625em 0;
    &__items{
      margin-top: .9375em;
      @media screen and (min-width: 577px){
        grid-template-columns: 16.4375em calc(100% - 34.75em) 16.4375em;
      }
    }
  }

  .slider{
    .swiper{
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        padding-right: .9375em;
      }
    }
  }

  .main-slider{
    .swiper-button{
      display: none;
    }
  }

  .main-banner{
    padding: 2.375em 1.5625em;
    border-radius: .375em;
    &__content{
      width: 23.25em;
    }
    &__desc{
      margin-top: 1.66em;
    }
    &__nav{
      margin-top: 1.5625em;
    }
    &__img{
      right: 0;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__sale{
      top: 0;
      right: -6.6875em;
      width: 5.6875em;
    }
  }

  .main-item,
  .main-item2{
    min-height: 13.75em;
    border-radius: .375em;
    padding: 3em .9375em;
  }

  .main-item{
    &__img{
      right: -2em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__nav{
      margin-top: .9375em;
    }
    &._type2{
      .main-item__img{
        right: 0;
      }
    }
  }

  .main-item2{
    .main-item__img{
      right: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: .9375em 0 2.5em;
  }

  .main-banner{
    padding: 2.375em .9375em;
    min-height: 33.875em;
    &__img{
      height: auto;
      width: 100%;
      top: unset;
      bottom: 0;
      img{
        width: 100%;
        height: auto;
      }
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__sale{
      top: unset;
      bottom: -.75em;
      right: 1.875em;
    }
  }

  .main-item{
    padding-top: 2.25em;
    padding-bottom: 2.25em;
    &__content{
      max-width: 13.75em;
    }
    &__desc{
      max-width: 12.3125em;
    }
    &__link2{
      right: .625em;
      bottom: .625em;
    }
    &._type2{
      .main-item__img{
        ._view2{
          display: none;
        }
        ._view3{
          display: block;
        }
      }
    }
  }

  .main-item2{
    padding: 1.125em .9375em;
    min-height: 8.125em;
    &._col1-2{
      grid-column: 1/2;
      grid-row: 2/3;
    }
    &__desc{
      display: none;
    }
    .main-item__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }
}
