.header{
  position: relative;
  z-index: 5;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 12.8125em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top,
.header-middle{
  &__inner{
    display: flex;
    align-items: center;
  }
}

.header-top{
  background-color: $gray;
  &__inner{
    min-height: 2.625em;
    justify-content: flex-end;
  }
}

.header-middle{
  padding: 1.25em 0;
  &__inner{
    position: relative;
    justify-content: space-between;
  }
}

.header-contacts{
  margin-right: auto;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      padding-right: 1.25em;
      margin-right: 1.25em;
      border-right: $border3;
    }
    img{
      width: 1em;
      margin-right: .25em;
    }
    &--mail{
      color: $blue2;
      span{
        text-decoration: underline;
      }
    }
  }
}

.nav{
  border-left: $border3;
  margin-left: 1.25em;
  padding-left: 1.25em;
  .menu{
    display: flex;
    align-items: center;
    color: $p;
    li{
      @include lastM(0 1.25em 0 0);
    }
    a{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.burger{
  display: none;
}

.header-action{
  display: flex;
  align-items: center;
  &__item{
    width: 1.25em;
    @include center;
    @include lastM(0 1.875em 0 0);
    position: relative;
    img{
      width: 100%;
    }
  }
  .pin{
    position: absolute;
    height: 1em;
    min-width: 1em;
    border-right: .375em;
    top: -.4375em;
    right: -.5em;
    span{
      font-size: .75em;
    }
  }
}

.catalog-btn{
  padding: 0 1.875em;
  height: 2.75em;
  margin-left: 1.875em;
  &__icon{
    display: block;
    width: 1.25em;
    flex-shrink: 0;
    position: relative;
    margin-right: .75em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      @include centerAbsolute;
      opacity: 0;
    }
  }
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

.header-search{
  flex-grow: 1;
  margin: 0 1.875em;
  .search-field{
    .fg{
      input{
        height: 2.75em;
      }
    }
  }
  &__btn{
    display: none;
  }
}

.nav2{
  .menu{
    height: 2.75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a{
      font-size: 1em;
      line-height: 144%;
    }
    ._sale{
      font-weight: 500;
      color: $sh;
      a{
        display: flex;
        align-items: center;
        &:before{
          content: '';
          flex-shrink: 0;
          width: 1em;
          height: 1em;
          background-image: url(../img/sale.svg);
          @include bgImgSize(100%);
          margin-right: .375em;
        }
      }
    }
  }
}

.m-search{
  position: relative;
  &__result{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    box-shadow: 0 .25em 2.5em 0 rgba(2, 38, 80, 0.13);
    border-radius: 0 0 .25em .25em;
  }
  &__items{
    @include scroll;
    overflow-y: auto;
    max-height: 25em;
  }
  &__nav{
    padding: 0 1.25em 1em;
  }
  &__link{
    height: 2.1875em;
    font-weight: 500;
    span{
      font-size: 0.875em;
    }
  }
  &__items{
    padding: 1em 1.25em;
    @include grid100gap(1em);
  }
  &._show{
    z-index: 3;
    .m-search__result{
      opacity: 1;
      pointer-events: unset;
    }

  }
}

.search-item{
  display: flex;
  align-items: center;
  &__img{
    width: 3.875em;
    height: 3.875em;
    flex-shrink: 0;
    @include center;
    border-radius: .25em;
    background: $gray;
    margin-right: 1em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    flex-grow: 1;
    @include grid100gap(.25em);
  }
  &__btn{
    flex-shrink: 0;
    width: 6.3125em;
    height: 2.1875em;
    padding: 0;
    margin-left: 1em;
  }
}

.empty-search{
  padding: 1em 1.25em;
  .empty-message{
    padding-top: .625em;
    padding-bottom: .625em;
    margin: 0;
    min-height: unset;
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 10em;
    }
  }

  .header-top{
    &__inner{
      min-height: 2.125em;
    }
  }

  .header-contacts{
    &__item{
      &--address{
        display: none;
      }
    }
  }

  .nav{
    margin-left: .9375em;
    padding-left: .9375em;
    .menu{
      li{
        @include lastM(0 .9375em 0 0);
      }
      a{
        font-size: .8125em;
      }
    }
  }

  .header-middle{
    padding: .625em 0;
  }

  .catalog-btn{
    flex-shrink: 0;
    order: -1;
    margin: 0 .9375em 0 0;
    width: 1.25em;
    height: 1.25em;
    padding: 0;
    background-color: transparent !important;
    border-right: 0;
    background-image: url(../img/burger3.svg);
    @include bgImgSize(100%);
    &__icon,
    span{
      display: none;
    }
  }

  .header-bottom{
    padding: .5em 0;
  }

  .nav2{
    .menu{
      height: 2.25em;
    }
    ._hide-tablet{
      display: none;
    }
  }

  .header-search{
    width: 17.3125em;
    display: flex;
    justify-content: flex-end;
    &__wrap{
      flex-shrink: 0;
      position: relative;
      min-width: 100%;
    }
    &._show{
      .header-search__wrap{
        width: 30em;
      }
    }
  }

  .m-search{
    &__items{
      gap: .25em;
      max-height: 26em;
      padding-top: .75em;
      padding-bottom: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    border-bottom: $border;
    .contact-socials__item{
      margin-right: .625em;
    }
  }

  .header-top{
    &__inner{
      justify-content: space-between;
    }
  }

  .nav{
    display: none;
  }

  .header-action{
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 20;
    padding: .9375em 1.875em;
    box-shadow: 0 -2px 19px 0 rgba(7, 37, 70, 0.27);
    background-color: #fff;
    justify-content: space-between;
    &__item{
      margin: 0 !important;
    }
  }

  .catalog-btn{
    margin: 0;
  }

  .header-search{
    position: static;
    width: auto;
    margin: 0;
    flex-grow: unset;
    &._open{
      .header-search__wrap{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__wrap{
      opacity: 0;
      pointer-events: none;
      position: absolute;
      width: 100% !important;
      top: 50%;
      transform: translateY(-50%);
    }
    &__btn{
      @include center;
      width: 1.25em;
      img{
        width: 100%;
      }
    }
  }

  .header-bottom{
    display: none;
  }

  .search-item{
    position: relative;
    &__btn{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
}
