.categories{
  padding: 5em 0;
  background-color: $gray;
  margin-bottom: 2.5em;
}

.categories-slider{
  margin-top: 2.25em;
  .swiper{
    &-slide{
      width: calc(100% / 6);
    }
  }
}

.category-card{
  background-color: #fff;
  padding: 1.25em .75em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-shadow: 0 4px 20px 0 rgba(133, 152, 199, 0.05);
  border-radius: .75em;
  height: 100%;
  border: 2px solid #fff;
  &:hover{
    color: $black;
    border-color: $blue2;
  }
  &__img{
    height: 7.8125em;
    @include center;
    margin-bottom: .25em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
  }
  &__count{
    font-size: .875em;
    line-height: 125%;
    color: $blue2;
  }
}

.make-product{
  padding: 2.5em 0;
  &__inner{
    padding: 3.75em;
    background: radial-gradient(217.72% 211.57% at 29% -38%,#404863,#404863 9.513%,#151b30 84.5%);
    color: #fff;
    border-radius: .75em;
    position: relative;
    overflow: hidden;
  }
  &__bg{
    position: absolute;
    bottom: 0;
    left: 29.4375em;
    width: 34.0625em;
  }
  &__content{
    max-width: 26.25em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    max-width: 26em;
    font-size: 1em;
    line-height: 144%;
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2.375em;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 46.375em;
  }
}


@media screen and (max-width: 992px){
  .categories{
    padding: 2.5em 0;
  }

  .categories-slider{
    margin-top: 1.875em;
    position: static;
    .swiper{
      &-slide{
        width: 25%;
      }
    }
  }

  .category-card{
    padding: 1.25em .25em;
    &__img{
      height: 5.625em;
      margin-bottom: .5em;
    }
    &__info{
      gap: 0;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
      font-weight: 500;
    }
    &__count{
      font-size: .875em;
      line-height: 125%;
      color: $blue2;
    }
  }

  .make-product{
    padding: 1.25em 0;
    &__inner{
      padding: 2.625em 1.5625em;
    }
    &__bg{
      left: 13.25em;
      top: 0;
      bottom: unset;
    }
    &__content{
      max-width: 20.75em;
    }
    &__nav{
      margin-top: 1.625em;
    }
    &__img{
      width: 33em;
      right: -6.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .categories-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .make-product{
    &__inner{
      padding: 2.5em .9375em 14.25em;
    }
    &__bg{
      left: unset;
      right: -1.875em;
      top: 13.625em;
      width: 13.5625em;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__img{
      width: 26em;
      right: -2em;
    }
  }
}
