.text{
  li,
  p{
    font-size: 1.0625em;
    line-height: 135%;
  }
  p{
    @include lastM(0 0 .75em);
  }
  img{
    max-width: 100%;
    display: block;
  }
}

.text{
  color: $grayText;
  h2,
  h3{
    color: $black;
    font-size: 2.375em;
    line-height: 116%;
    font-weight: 600;
    margin-bottom: .375em;
  }
  ul,
  ol{
    @include lastM(0 0 .75em);
  }
  b{
    font-weight: 700;
  }
  hr{
    height: 1em;
    opacity: 0;
    display: block;
  }
  ul{
    @include grid100gap(.5em);
    li{
      position: relative;
      padding-left: 1.35em;
      &:before{
        content: '';
        position: absolute;
        top: .25em;
        left: 0;
        width: .94em;
        height: .94em;
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
      }
    }
  }
  &._list2{
    ul{
      gap: .625em;
      li{
        padding-left: 2em;
        &:before{
          width: 1.33em;
          height: 1.33em;
          top: .2em;
          background-image: url(../img/check6.svg);
        }
      }
    }
  }
  &._list3{
    ul{
      gap: .19em;
      padding-left: .6em;
      li{
        padding-left: .8em;
        position: relative;
        &:before{
          content: '•';
          position: absolute;
          top: 0;
          left: 0;
          background-image: unset;
          width: unset;
          height: unset;
        }
      }
    }
  }
  &._small{
    p{
      font-size: 1em;
      line-height: 144%;
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 992px){
  .text {
    li,
    p {
      font-size: 1em;
      line-height: 144%;
    }
    h2,h3{
      font-size: 1.5em;
    }
  }
}


