.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    backdrop-filter: blur(20px);
    background: rgba(21, 27, 48, 0.8);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 43.125em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: .75em;
        display: inline-block;
    }
    &__top{
        text-align: center;
        margin-bottom: 1.25em;
    }
    .close-btn{
        width: 1.625em;
        @include center;
        position: absolute;
        top: .75em;
        right: .75em;
    }
}

.modal-product{
    display: grid;
    grid-template-columns: 6.625em calc(100% - 6.625em - .875em);
    gap: .875em;
    margin-bottom: 1.25em;
    align-items: flex-start;
    &__img{
        @include imgCover(84%);
        border-radius: .3125em;
    }
    &__content{
        margin-top: -.25em;
    }
    &__articul{
        margin-top: .5em;
        span{
            color: $sh;
        }
    }
    &__bottom{
        margin-top: .125em;
    }
    &__prices{
        display: flex;
        align-items: flex-end;
        &-title{
            font-size: .875em;
            padding: 0 .25em .375em 0;
        }
    }
    &__hint{
        margin-top: .25em;
        color: #727378;
        max-width: 39em;
    }
}

.thank-modal{
    .modal__dialog{
        width: 28.75em;
    }
    &__content{
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        img{
            width: 2.75em;
            margin-bottom: .75em;
        }
    }
    &__desc{
        margin-top: .5em;
    }
    &__btn{
        margin-top: 1em;
    }
}

.product-modal{
    &._type2{
        .product-modal__img{
            padding: 0;
        }
        .gallery__img{
            padding-bottom: 126%;
            min-height: 100%;
            border-radius: inherit;
        }
    }
    .modal__dialog{
        width: 58.25em;
        padding: 0;
    }
    &__grid{
        display: grid;
        grid-template-columns: 27.875em calc(100% - 27.875em);
    }
    &__img{
        padding: 2.625em 0;
        display: flex;
        align-items: center;
        background-image: url(../img/gallery-bg.svg);
        background-size: cover;
        border-radius: .75em 0 0 .75em;
    }
    &__content{
        padding: 2.5em;
    }
    .gallery__img{
        background: transparent;
        width: 100%;
    }
    &__info{
        display: flex;
        align-items: center;
        margin-top: .75em;
        &-item{
            display: flex;
            align-items: center;
            margin-right: 1em;
            .text14{
                margin-right: .7em;
            }
        }
    }
    .product-props{
        padding-top: 0;
        border: none;
        .text20{
            font-size: 1em;
        }
    }
    .product-card__prices{
        margin-top: 1.25em;
    }
    .product-card__price{
        font-size: 1.5em;
        font-weight: 500;
        margin: 0;
    }
    .product-card__price2{
        font-size: .75em;
        order: 2;
    }
    .product-card__sale{
        margin: 0 .75em;
    }
    &__nav{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .375em));
        gap: 1.25em .75em;
        margin-top: 1.75em;
        .m-btn-blue-outline{
            border-width: 2px;
        }
    }
    .cartcalc{
        height: 3.125em;
        border: $border;
        background-color: transparent;
        width: 100%;
        grid-template-columns: 3.125em calc(100% - 6.25em) 3.125em;
    }
}

.modal-prices{
    margin-top: 1.25em;
    &__wrap{
        display: flex;
        align-items: flex-end;
        margin-top: .5em;
    }
    &__num{
        font-size: 2.375em;
        font-weight: 600;
    }
    &__from{
        margin: 0 .75em .25em 0;
    }
    &__hint{
        color: #727378;
        margin-top: .5em;
    }
}

.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    background: #fff;
    z-index: 21;
    padding: 1em 0;
    width: 100%;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0px -1px 55px 0px rgba(0, 0, 0, 0.08);
    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__content{
        @include grid100gap(.5em);
        max-width: 57.5em;
    }
    &__title{
        font-weight: 500;
    }
    &__desc{
        color: $p;
        a{
            color: $blue2;
            text-decoration: underline;
        }
    }
    &__nav{
        display: flex;
        margin-left: .875em;
        flex-shrink: 0;
    }
    &__btn{
        max-width: 100%;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            width: 36.625em;
            padding: 1.25em;
        }
        &__top{
            margin-bottom: 1em;
        }
    }

    .modal-product{
        grid-template-columns: 4.375em calc(100% - 4.375em - .9375em);
    }

    .thank-modal {
        .modal__dialog {
            width: 26.625em;
        }
    }

    .cookie-modal{
        padding: 1.25em 0;
        &__desc{
            font-size: .875em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }

    .modal-form{
        .consult-form__bottom{
            display: flex;
            flex-direction: column;
            text-align: center;
        }
        .consult-form__btn{
            width: auto;
        }
    }

    .modal-product{
        &__bottom{
            margin-top: 1em;
            width: calc(100% + 4.375em + .9375em);
            margin-left: calc((4.375em + .9375em) * -1);
        }
    }

    .cookie-modal{
        &__inner{
            display: block;
        }
        &__nav{
            margin: .5em 0 0;
        }
    }
}
