.product-detail{
  @include grid100gap(2.5em);
  &__nav{
    display: flex;
    border-bottom: $border;
  }
  &__link{
    margin-right: 1.875em;
    height: 2.5em;
    margin-bottom: -1px;
    display: flex;
    align-items: center;
    color: #64666C;
    font-weight: 700;
    position: relative;
    &:last-child{
      margin-right: 0;
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $sh;
      opacity: 0;
      transition: inherit;
    }
    span{
      font-size: 1em;
    }
    &:hover{
      color: $black;
    }
    &._active{
      color: $black;
      &:before{
        opacity: 1;
      }
    }
  }
}

.feedback,
.product-props2,
.product-desc{
  padding: 2.5em;
  border-radius: .75em;
  border: $border;
}

.product-desc{
  &__text{
    margin-top: 1.25em;
    color: $p;
  }
}

.product-props2{
  .product-props__items{
    display: block;
    column-count: 2;
    column-gap: 1.875em;
  }
}

.feedback{
  border: none;
  background-color: $gray;
  @include grid100gap(2.25em);
}

.feedback-auth,
.feedback-form,
.form-success{
  background-color: #fff;
  border-radius: .75em;
  padding: 1.875em;
}

.feedback-form{
  @include grid100gap(1.875em);
  &__nav{
    display: flex;
    align-items: center;
    max-width: 39em;
  }
  &__btn{
    margin-right: 1em;
    flex-shrink: 0;
  }
  &__hint{
    a{
      color: $blue2;
    }
  }
}

.form-success{
  &__top{
    display: flex;
    align-items: flex-start;
    img{
      width: 2.125em;
      margin-right: .625em;
    }
  }
  &__text{
    @include grid100gap(.25em);
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.feedback-auth{
  .empty-message{
    padding: .75em .9375em;
    margin-top: 1.75em;
    justify-content: unset;
    align-items: flex-start;
    a{
      color: $blue2;
      text-decoration: underline;
    }
  }
}

.feedback-messages{
  @include grid100gap(2.5em);
}

.message-item{
  padding-left: 1.25em;
  border-left: 2px solid $comm;
  max-width: 43.75em;
  &--answer{
    margin-top: 1.75em;
    border-color: #08A56C;
    padding-left: 1em;
    .message-item__name{
      color: $blue2;
    }
  }
  &__date{
    margin-top: .25em;
  }
  &__desc{
    margin-top: .75em;
  }
}

.categories-slider3{
  .swiper-slide{
    width: 25%;
  }
}

.product-alert{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  background-color: #fff;
  box-shadow: 0 10px 21px 0 rgba(0, 0, 0, 0.1), 0 39px 39px 0 rgba(0, 0, 0, 0.09), 0 87px 52px 0 rgba(0, 0, 0, 0.05), 0 155px 62px 0 rgba(0, 0, 0, 0.01), 0 242px 68px 0 rgba(0, 0, 0, 0);
  z-index: 30;
  position: fixed;
  top: 6.25em;
  right: 3.125em;
  width: 23.5625em;
  padding: .75em .9375em;
  display: grid;
  grid-template-columns: 5em calc(100% - 6.25em);
  gap: 1.25em;
  align-items: center;
  border-radius: .3125em;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    width: 1.125em;
    top: .625em;
    right: .625em;
    svg{
      width: 100%;
    }
    &:hover{
      g{
        opacity: 1;
      }
    }
  }
  &__img{
    @include imgCover(100%);
    background-color: $gray;
  }
  &__mess{
    display: flex;
    align-items: center;
    margin-bottom: .25em;
    img{
      width: .875em;
      margin-right: .375em;
    }
  }
  &__title{
    @include truncate(2);
  }
  &__price{

  }
}

._add-cart-btn{
  ._view2{
    display: none;
  }
  &._active{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .product-detail{
    gap: 1.875em;
    &__nav{
      padding-left: .9375em;
    }
    &__link{
      margin-right: 1.25em;
      white-space: nowrap;
      flex-shrink: 0;
      span{
        font-size: .875em;
      }
    }
  }

  .product-props2,
  .product-desc{
    padding: .9375em;
    border-radius: 0;
    border: none;
  }

  .product-desc{
    &__text._small{
      margin-top: .25em;
      p{
        @include lastM(0 0 .25em);
      }
    }
  }

  .product-props2{
    .product-props__items{
      column-count: 1;
    }
  }

  .feedback{
    padding: .9375em;
    border-radius: .3125em;
    gap: .9375em;
  }

  .feedback-form{
    gap: .9375em;
    padding: .9375em;
    margin-top: -.5em;
    &__title{
      margin-bottom: -.5em;
    }
  }

  .form-success{
    padding: .9375em;
    &__nav{
      margin-top: .75em;
    }
  }

  .feedback-auth{
    padding: .9375em;
    .empty-message{
      margin-top: 1.25em;
    }
  }

  .feedback-messages{
    gap: 1.25em;
  }

  .categories-slider3{
    .swiper-slide{
      width: calc(100% / 3);
    }
  }

  .product-alert{
    width: 20.625em;
    padding: .625em;
    grid-template-columns: 3.5em calc(100% - 3.5em - .9375em);
    gap: .93375em;
    right: 1.5625em;
    top: 2.5em;
    align-items: flex-start;
  }
}


@media screen and (max-width: 576px){
  .product-detail{
    gap: 0;
    &__nav{
      @include fullScreenMob;
      @include nullScroll;
      overflow-x: auto;
    }
  }

  .product-props2,
  .product-desc{
    padding-left: 0;
    padding-right: 0;
  }

  .feedback{
    @include fullScreenMob;
    padding-top: 2.5em;
    padding-bottom: 2.5em;
    border-radius: 0;
  }

  .feedback-form{
    &__nav{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .625em;
    }
  }

  .form-success{
    &__btn{
      width: 100%;
    }
  }

  .categories-slider3{
    .swiper-slide{
      width: 50%;
    }
    .category-card2{
      &__img{
        padding-bottom: 100%;
      }
    }
  }

  .product-alert{
    width: calc(100% - 1.875em);
    right: .9375em;
    top: 3.75em;
  }
}
