.error404{
  &__grid{
    display: grid;
    grid-template-columns: 43.625em calc(100% - 48.625em);
    gap: 2.25em 5em;
    margin-top: 1.875em;
  }
  &__img{
    width: 100%;
    display: block;
    filter:
            drop-shadow(3px 5px 14px rgba(0, 0, 0, 0.08))
            drop-shadow(13px 21px 25px rgba(0, 0, 0, 0.07))
            drop-shadow(29px 48px 34px rgba(0, 0, 0, 0.04))
            drop-shadow(52px 85px 40px rgba(0, 0, 0, 0.01))
            drop-shadow(81px 132px 43px rgba(0, 0, 0, 0))
  ;
  }
  &__col2{
    padding-top: .5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .search-field{
    margin-top: 1em;
    width: 100%;
  }
  &__link{
    margin-top: auto;
    padding: 0 3.125em;
    height: 3.875em;
    img{
      margin-left: .75em;
      width: 1.375em;
    }
  }
}

.search-field{
  .fg{
    input{
      background-color: $gray;
      height: 3.1875em;
      padding-right: 2.75em;
    }
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    right: 1.25em;
    @include center;
    &:hover{
      path{
        opacity: 1;
      }
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.conf{
  padding: 0 0 5em;
  &__text{
    margin-top: 1.875em;
    font-weight: 400;
    a{
      color: $blue2;
      font-weight: 700;
      text-decoration: underline;
    }
    p,li{
      font-size: .9375em;
      line-height: 180%;
    }
    p{
      @include lastM(0 0 .5em);
    }
  }
  &__hint{
    padding: 2em 2.1875em;
    background: $gray;
    margin-top: 1.56em;
    .text{
      margin: 0;
      p{
        font-size: 1em;
        line-height: 144%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    &__grid{
      max-width: 100%;
      width: 28.5em;
      margin-left: auto;
      margin-right: auto;
      grid-template-columns: 100%;
    }
    &__img{
      max-width: 22.5em;
      margin: 0 auto;
    }
    &__col2{
      padding: 0;
      align-items: center;
      text-align: center;
    }
    &__link{
      margin-top: 2.1875em;
    }
  }

  .conf{
    padding-bottom: 2.5em;
    &__hint{
      padding: .75em .9375em;
    }
    &__text{
      margin-top: 1.25em;
      p,li{
        font-size: .875em;
        line-height: 164%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    &__grid{
      margin-top: .875em;
    }
    &__img{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
  }

  .conf{
    &__hint{
      margin-top: .625em;
    }
  }
}
