.current-orders{
  margin-top: 2em;
}

.orders-table{
  margin-top: 1.25em;
  border: 1px solid #eaeaea;
  border-radius: .3125em;
  &__grid{
    display: grid;
    grid-template-columns: 13.75em 15.75em calc(100% - 44.5em) 15em;
  }
  &__head{
    border-bottom: 1px solid #EBEBEB;
    &-col{
      min-height: 2.75em;
      display: flex;
      align-items: center;
      font-weight: 600;
      padding-left: 1.25em;
    }
  }
}

.order-row{
  position: relative;
  border-bottom: 1px solid #eaeaea;
  &:last-child{
    border-bottom: none;
  }
  &__link{
    @include center;
    border-radius: .3125em;
    background-color: $gray;
    svg{
      width: .3125em;
    }
    &:hover{
      background-color: $darkBlue;
      path{
        stroke: #fff;
      }
    }
    @media screen and (min-width: 993px){
      position: absolute;
      top: 50%;
      right: 2.625em;
      transform: translateY(-50%);
      width: 3.125em;
      height: 3.125em;
      span{
        display: none;
      }
    }
  }
  &__col{
    min-height: 4.125em;
    display: flex;
    align-items: center;
    padding-left: 1.25em;
    &:last-child{
      padding-right: 5.75em;
    }
  }
  &__title{
    display: none;
  }
  &__price{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 500;
  }
}

.order-status{
  @include center;
  max-width: 100%;
  width: 11.6875em;
  height: 2.125em;
  border-radius: .3125em;
  text-transform: uppercase;
  border: 1px solid #ff6915;
  font-weight: 500;
  color: #ff6915;
  &__round{
    background-color: #ff6915;
    width: 0.5em;
    height: 0.5em;
    flex-shrink: 0;
    border-radius: 50%;
    margin-right: .5em;
  }
  &._red{
    border-color: #F81D2A;
    color: #F81D2A;
    .order-status__round{
      background-color: #F81D2A;
    }
  }
  &._green{
    border-color: #11A342;
    color: #11A342;
    .order-status__round{
      background-color: #11A342;
    }
  }
}

.order-item{
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: center;
    color: #646464;
    margin-top: .75em;
  }
  &__date{
    span{
      color: #141414;
    }
  }
  &__status{
    margin-left: .25em;
    display: flex;
    align-items: center;
    .order-status{
      width: 6.25em;
      margin-left: .625em;
    }
  }
  &__back{
    height: 2.3125em;
    @include center;
    padding: 0 .9375em;
    border-radius: .3125em;
    background-color: $gray;
    color: $blue2;
    &:hover{
      background-color: $blue2;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    svg{
      margin-right: .5em;
      width: .375em;
    }
    span{
      font-size: 1em;
    }
  }
}

.order-detail{
  border-radius: .3125em;
  border: $border;
  margin-top: 2em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 19em) 7em 12em;
    padding: 0 1.25em;
    align-items: center;
  }
  &__head{
    height: 3.125em;
    font-weight: 600;
  }
  &__col{
    &:nth-child(2){
      text-align: center;
    }
    &:last-child{
      text-align: right;
    }
  }
}

.detail-item{
  min-height: 3em;
  padding-top: .5em;
  padding-bottom: .5em;
  border-top: $border2;
  &:nth-child(even){
    background-color: $gray;
  }
  &._type2{
    min-height: 3.375em;
    padding-left: 1.25em;
    padding-right: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__col{
    display: flex;
    align-items: center;
  }
  &__col2{
    text-align: center;
  }
  &__col3{
    text-align: right;
  }
  &__count{
    color: #7a7a7e;
    margin-left: .625em;
  }
  &__price{
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 500;
  }
  &__desc{
    color: #494950;
  }
  &__mob-title{
    display: none;
  }
}

.warehouse{
  margin-top: 2em;
  display: grid;
  grid-template-columns: 50% 50%;
  border: $border3;
  border-radius: .3125em;
  overflow: hidden;
  &__col1{
    padding: 1.5em 1.25em;
    border-right: $border3;
  }
  &__items{
    margin-top: 1.5em;
  }
  &__item{
    display: flex;
    align-items: flex-start;
    @include lastM(0 0 1.5em);
    img{
      width: 1em;
      margin-right: .375em;
    }
    &-content{
      margin-top: -.25em;
    }
    &-title{
      font-weight: 500;
    }
  }
  &__map{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
}


@media screen and (max-width: 992px){
  .orders-table{
    border: none;
    &__head{
      display: none;
    }
    &__rows{
      @include grid100gap(1.5625em);
    }
  }

  .order-row{
    grid-template-columns: 100%;
    gap: .31em;
    border: 1px solid #EBEBEB;
    padding: .94em;
    &__col{
      padding: 0 !important;
      min-height: 1.875em;
      justify-content: space-between;
    }
    &__title{
      display: block;
      font-size: .875em;
      line-height: 130%;
      font-weight: 500;
    }
    &__link{
      height: 2.375em;
      text-transform: uppercase;
      font-weight: 700;
      color: $blue2;
      &:hover{
        background-color: $blue2;
        border-color: $blue2;
        color: #fff;
        path{
          fill: #fff;
        }
      }
      span{
        font-size: 0.75em;
        line-height: 130%;
        letter-spacing: 0.01em;
      }
      svg{
        margin-left: .375em;
      }
    }
  }

  .order-status{
    width: 6.375em;
  }

  .order-detail{
    margin-top: 1.75em;
    &__grid{
      display: flex;
      justify-content: space-between;
    }
    &__col{
      &:nth-child(2){
        display: none;
      }
    }
  }

  .detail-item{
    &._type1{
      flex-wrap: wrap;
      .detail-item__col{
        width: 100%;
        justify-content: space-between;
        margin-bottom: .25em;
      }
      .detail-item__col2{
        display: flex;
      }
    }
    &__mob-title{
      display: block;
      margin-right: .25em;
    }
    &__desc{
      max-width: 9.5em;
      text-align: right;
    }
  }
}


@media screen and (max-width: 576px){
  ._hide-menu-btn{
    .menu-btn{
      display: none;
    }
  }

  .order-item{
    &__col2{
      display: none;
    }
    &__info{
      margin-top: .5em;
      display: block;
    }
    &__status{
      margin: 0;
    }
  }

  .warehouse{
    grid-template-columns: 100%;
    &__col1{
      padding: 1em 1.25em;
      border-right: none;
      border-bottom: $border3;
    }
    &__map{
      min-height: 13.125em;
    }
  }
}


