.tabs{
  display: flex;
}

.tab{
  height: 2.5em;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 700;
  color: #64666c;
  transition: all .3s;
  flex-shrink: 0;
  @include lastM(0 1.875em 0 0);
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: $sh;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: 1em;
    line-height: 150%;
  }
  &:hover{
    color: $black;
    cursor: pointer;
  }
  &._active{
    color: $black;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .tabs{
    @include fullScreenTablet;
    @include nullScroll;
    overflow-x: auto;
  }

  .tab{
    font-weight: 600;
    height: 2.375em;
    @include lastM(0 1.25em 0 0);
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .tabs{
    @include fullScreenMob;
  }
}
