.catalog-categories{
  display: grid;
  grid-template-columns: repeat(5,calc(20% - 1em));
  gap: 1.25em;
}

.category-link{
  display: flex;
  align-items: center;
  min-height: 3.75em;
  background-color: $borderColor;
  border: 2px solid $borderColor;
  border-radius: .25em;
  padding: .125em .875em;
  &:hover,
  &._active{
    background-color: $gray;
    border-color: $blue2;
    color: $black;
  }
  &__img{
    width: 2.5em;
    height: 2.5em;
    flex-shrink: 0;
    margin-right: .75em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      border-radius: .25em;
    }
  }
}

.filter2{
  margin-top: 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sort{
  &__items{
    display: flex;
    margin-left: .75em;
  }
  &__select{
    display: none;
  }
}

.sort-item{
  background-color: $gray;
  border: $border;
  padding: 0 .75em;
  display: flex;
  align-items: center;
  height: 1.75em;
  margin-right: .75em;
  border-radius: .25em;
  transition: all .3s;
  cursor: pointer;
  &:hover,
  &._active{
    background-color: $blue2;
    border-color: $blue2;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  &._reverse{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
  &__img{
    width: 1em;
    height: 1em;
    margin-right: .625em;
    position: relative;
    svg{
      width: 100%;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }
}

.pagination2{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
  }
  &__item{
    margin-left: .86em;
    width: 2em;
    height: 2em;
    font-size: .875em;
    border-radius: .28em;
    border: $border3;
    @include center;
    color: $p;
    transition: all .3s;
    cursor: pointer;
    &:hover,
    &._active{
      border-color: $black;
      color: $black;
    }
  }
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
  margin-top: .5em;
  &__clear{
    margin-top: .75em;
    color: $blue2;
    border-bottom: 1px dashed $blue2;
    &:hover{
      color: $darkBlue;
      border-color: $darkBlue;
    }
  }
}

.selected-item{
  display: flex;
  align-items: center;
  margin: .75em 1.25em 0 0;
  &__title{
    margin-right: .4375em;
    color: $p;
  }
  &__remove{
    margin-left: .4375em;
    width: 1.1875em;
    @include center;
    img{
      width: 100%;
    }
  }
}

.catalog-items{
  row-gap: 3.125em;
  margin-top: 2.25em;
}

.catalog-bottom{
  margin-top: 2.8125em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__more{
    display: flex;
    align-items: center;
    color: $blue2;
    &:hover{
      color: $darkBlue;
      path{
        stroke: $darkBlue;
      }
      span{
        border-color: $darkBlue;
      }
    }
    svg{
      margin-right: .625em;
      width: 1.4375em;
    }
    span{
      transition: inherit;
      font-size: 1em;
      line-height: 144%;
      border-bottom: 1px dashed rgba($blue2,.5);
    }
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2.5em;
    width: 100%;
  }
  .pagination{
    margin: 0;
  }
}

.empty-message{
  margin-top: 1.875em;
  padding: 1em 1.25em;
  min-height: 4em;
  @include center;
  background-color: $gray;
  border: 1px dashed $blue;
  border-radius: .3125em;
  color: $darkBlue;
  img{
    width: 1.5em;
    margin-right: .625em;
  }
}


@media screen and (max-width: 992px){
  .catalog-categories{
    display: flex;
    gap: unset;
    @include fullScreenTablet;
    overflow-x: auto;
    @include nullScroll;
  }

  .category-link{
    width: 13em;
    flex-shrink: 0;
    @include lastM(0 .625em 0 0);
  }

  .filter2{
    margin-top: .5em;
    display: block;
  }

  .pagination2{
    display: none;
  }

  .selected-filters{
    margin-top: 0;
    width: calc(100% + .9375em);
  }

  .selected-item{
    margin-right: .9375em;
    &__title,
    &__desc{
      font-size: .875em;
    }
  }

  .catalog-items{
    margin-top: 1.875em;
    grid-template-columns: repeat(4,calc(25% - 2.8125em / 4));
    gap: 1.5625em .9375em;
  }

  .catalog-bottom{
    margin-top: 2.5em;
    &__count,
    &__more{
      display: none;
    }
    &__wrap{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-categories{
    @include fullScreenMob;
  }

  .sort{
    &__items{
      display: none;
    }
    &__select{
      display: block;
      margin-left: .5em;
      flex-grow: 1;
    }
  }

  .catalog-items{
    margin-top: 1.25em;
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
  }

  .catalog-bottom{
    margin-top: 1.875em;
    &__wrap{
      justify-content: center;
    }
  }

  .empty-message{
    margin-top: 1.25em;
  }
}
