.consult__inner,
.consult-form__fields{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .652em));
  gap: 1.25em;
}


.consult{
  overflow: hidden;
  padding-bottom: 5em;
  &__inner{
    padding: 3.75em;
    background-color: $gray;
    position: relative;
    .socials{
      margin-top: 2em;
    }
  }
  &__img{
    position: absolute;
    right: -10.6875em;
    bottom: -4.625em;
    width: 20.5em;
    filter: drop-shadow(12px 12px 5px rgba(163, 165, 187, 0.12))
            drop-shadow(30px 30px 50px rgba(53, 77, 113, 0.12));
  }
  &__subtitle{
    margin-top: .125em;
  }
  &__desc,
  &__desc2{
    max-width: 30em;
    font-size: 1em;
    line-height: 144%;
    margin-top: 1.25em;
    color: $p;
  }
  &__desc2{
    margin-top: .5em;
  }
}

.consult-form{
  &__bottom{
    margin-top: 1.875em;
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 12.8125em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 1em;
      margin-left: .75em;
    }
  }
  &__hint{
    font-size: .875em;
    line-height: 120%;
    color: $p;
    a{
      font-weight: 500;
      color: $blue2;
      &:hover{
        color: $blue;
      }
    }
  }
}

._full{
  grid-column: 1/-1;
}

.blog-collection{
  padding: 5em 0;
  margin-top: 2.5em;
  background-color: $gray;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.blog-slider{
  margin-top: 3.125em;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.blog-card{
  background-color: #fff;
  border-radius: .75em;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(60%);
    border-radius: .75em .75em 0 0;
  }
  &__content{
    padding: 1.875em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__desc{
    margin-top: 1em;
    font-size: .875em;
    line-height: 120%;
    color: $p;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.5em;
    display: flex;
  }
  &__link{
    font-size: 1em;
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .consult{
    padding-top: 2.5em;
    margin-top: -1.25em;
    &__inner{
      grid-template-columns: 100%;
      padding: 1.875em;
    }
    &__col{
      @media screen and (min-width: 577px){
        display: grid;
        grid-template-columns: 21.25em 12.5em;
        gap: 4.375em;
        .consult__desc2{
          margin: 0;
        }
        .socials{
          margin-top: .5em;
        }
      }
    }
    &__subtitle{
      font-size: 1em;
      line-height: 125%;
    }
    &__img{
      width: 14.25em;
      top: -2.75em;
      right: -5em;
    }
  }

  .consult-form{
    &__btn{
      height: 3.125em;
    }
  }

  .blog-collection{
    padding: 2.5em 0;
    margin-top: 1.25em;
    overflow: hidden;
    &__top{
      padding-right: 10em;
    }
  }

  .blog-slider{
    margin-top: 1.875em;
    position: static;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 18.125em;
      }
      &-nav{
        top: -.5em;
      }
    }
  }

  .blog-card{
    &__content{
      padding: .9375em;
    }
    &__desc{
      margin-top: .5em;
    }
    &__nav{
      padding-top: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult{
    &__subtitle{
      margin-top: .75em;
    }
    &__desc{
      margin-top: 1em;
    }
    &__desc2{
      margin-top: .625em;
    }
    .socials{
      margin-top: 1.25em;
    }
    &__img{
      width: 10.75em;
      top: -2.5em;
      right: -3.875em;
    }
  }

  .consult-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__bottom{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .75em;
    }
  }

  .blog-collection{
    .m-section__inner{
      padding-bottom: 6em;
    }
    &__top{
      padding: 0;
    }
    &__link{
      position: absolute;
      width: auto;
      padding: 0 1em;
      right: 50%;
      bottom: 1em;
      margin-right: .4375em;
    }
  }

  .blog-slider{
    margin-top: 1em;
    .swiper{
      &-nav{
        top: unset;
        right: 1.5em;
        transform: unset;
        bottom: .625em;
      }
    }
  }
}
