.categories-page{
  &__groups{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 2.5em 1.25em;
  }
}

.category-group{
  &:not(._full){
    @media screen and (min-width: 577px){
      .category-group__grid{
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 6.25em / 6));
    gap: 1.25em;
    margin-top: 1.75em;
    &._type2{
      grid-template-columns: repeat(4,calc(25% - .9375em));
    }
  }
  .category-card{
    border: $border2;
    outline: 1px solid transparent !important;
    box-shadow: unset;
    &:hover{
      border-color: $blue2;
      outline-color: $blue2 !important;
    }
  }
}

.category-card2{
  border-radius: .75em;
  position: relative;
  display: block;
  text-align: center;
  color: #fff;
  outline: 2px solid transparent !important;
  &:hover{
    color: #fff;
    outline-color: $blue2 !important;
  }
  &__img{
    border-radius: .75em;
    @include imgCover(70.78%);
    &:after{
      background: linear-gradient(180.00deg, rgba(7, 21, 37, 0) 43.913%,rgba(7, 21, 37, 0.69) 100%);
    }
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: .9375em;
  }
  .category-card__count{
    color: $blue;
  }
}

.cant-find{
  padding: 5em 0;
  &__inner{
    padding: 3.125em;
    background-color: $gray;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    background-image: url(../img/cant-find-bg.svg);
    background-repeat: no-repeat;
    background-position: right top;
    background-size: auto 100%;
  }
  &__img{
    width: 20.5em;
    top: -4.125em;
    right: 23em;
    position: absolute;
    filter: drop-shadow(.75em .75em .3125em rgba(163, 165, 187, 0.12))
            drop-shadow(1.875em 1.875em 3.125em rgba(53, 77, 113, 0.12));

  }
  &__info{
    max-width: 36.625em;
  }
  &__title{
    font-family: $font1;
  }
  &__desc{
    margin-top: .125em;
  }
  &__btn{
    img{
      margin-left: .75em;
      width: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .categories-page{
    &__groups{
      margin-top: 1.25em;
      gap: 1.875em 1.25em;
    }
  }

  .category-group{
    &:not(._full){
      @media screen and (min-width: 577px){
        .category-group__grid{
          grid-template-columns: repeat(2,calc(50% - .9375em / 2));
        }
      }
    }
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 2.8125em / 4));
      gap: .9375em;
      margin-top: .75em;
      &._type2{
        grid-template-columns: repeat(3,calc(100% / 3 - .625em));
      }
    }
  }

  .category-card2{
    &__img{
      padding-bottom: 78.6%;
    }
  }

  .cant-find{
    padding: 2.5em 0;
    &__inner{
      padding: 2.125em 1.5625em;
      background-image: url(../img/cant-find-bg2.svg);
    }
    &__img{
      width: 13.625em;
      top: -2em;
      right: 13.75em;
    }
    &__info{
      max-width: 18.375em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1em;
      line-height: 125%;
      font-weight: 600;
      margin-top: .75em;
    }
    &__btn{
      padding: 0 1.875em;
      height: 3.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .categories-page{
    &__groups{
      gap: 2.125em;
      grid-template-columns: 100%;
    }
  }

  .category-group{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      &._type2{
        grid-template-columns: 100%;
      }
    }
  }

  .category-card2{
    &__img{
      padding-bottom: 54.38%;
    }
  }

  .cant-find{
    overflow: hidden;
    padding-top: 1.25em;
    &__inner{
      display: block;
      background-image: url(../img/cant-find-bg3.svg);
      background-size: auto calc(100% - 2.75em);
      background-position: right center;
    }
    &__img{
      width: 11em;
      top: -2em;
      right: -2.5em;
    }
    &__info{
      max-width: 14.25em;
    }
    &__title{
      max-width: 7.9em;
    }
    &__btn{
      width: 100%;
      margin: 1.75em 0 0;
    }
  }
}
