.promo{
  padding: 2.5em 0  7.5em;
}

.promo-slider{
  margin-top: 1.875em;
  @media screen and (min-width: 577px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        display: grid;
        gap: 1.875em;
        grid-template-columns: repeat(6,calc(100% / 6 - 9.375em / 6));
        transform: unset !important;
        grid-auto-flow: dense;
      }
      &-slide{
        width: 100% !important;
        grid-column: span 2;
        padding-right: 0;
        &--big{
          grid-column: span 3;
          .promo-card{
            &__img{
              padding-bottom: 43%;
            }
          }
        }
      }
      &-nav{
        display: none;
      }
    }
  }
}

.promo-card{
  color: #fff;
  position: relative;
  &__img{
    @include imgCover(92.3%);
    border-radius: .75em;
    border: $border;
    ._view2{
      display: none;
    }
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 2em 2.5em;
    max-width: 23.125em;
  }
  &__title{
    font-weight: 600;
    font-family: $font2;
  }
  &__desc{
    max-width: 18.125em;
    margin-top: .25em;
    font-size: 1em;
    line-height: 144%;
    b{
      font-weight: 600;
    }
  }
  &__link{
    position: absolute;
    right: 2.5em;
    bottom: 2.5em;
  }
}

.sale{
  padding: 2.5em 0;
  color: #fff;
  &__inner{
    position: relative;
  }
  &__img{
    @include imgCover(27%);
    border-radius: .75em;
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    padding: 3.25em 3.75em;
    width: 45.625em;
    height: 100%;
    max-width: 100%;
  }
  &__desc{
    margin-top: 1em;
    font-size: 1em;
    line-height: 144%;
    color: $comm;
    max-width: 27.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .promo{
    padding: 1.25em 0;
  }

  .promo-slider{
    margin-top: 1em;
    @media screen and (min-width: 577px){
      .swiper{
        &-wrapper{
          gap: .9375em;
          grid-template-columns: repeat(6,calc(100% / 6 - .9375em * 5 / 6));
        }
        &-slide{
          &--big{
            .promo-card{
              &__img{
                padding-bottom: 59.8%;
              }
            }
          }
        }
      }
    }
  }

  .promo-card{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      padding: .9375em;
    }
    &__title{
      font-weight: 600;
      font-family: $font2;
    }
    &__desc{
      margin-top: .25em;
      font-size: .875em;
    }
    &__link{
      right: 1.25em;
      bottom: 1.25em;
    }
  }

  .sale{
    padding: 1.25em 0;
    &__img{
      padding-bottom: 45%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      padding: 3.125em 1.5625em;
      width: 26em;
    }
    &__nav{
      margin-top: 1.625em;
    }
  }
}


@media screen and (max-width: 576px){
  .promo{
    overflow: hidden;
    padding-bottom: 6.25em;
    position: relative;
  }

  .promo-slider{
    position: static;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-nav{
        display: flex;
        position: absolute;
        bottom: 1.25em;
        left: 50%;
        transform: translateX(-50%);
      }
      &-button{
        border: 1px solid rgba(215,218,226,.5);
        position: static;
        transform: unset;
        margin: 0 .3125em;
      }
    }
  }

  .promo-card{
    &__img{
      padding-bottom: 63.6%;
    }
    &__content{
      padding: 1.125em 1.5em;
    }
    &__link{
      width: 3.125em;
      height: 3.125em;
      svg{
        width: 1.25em;
      }
    }
  }

  .sale{
    &__img{
      padding-bottom: 151%;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__content{
      padding: 2.125em .9375em;
    }
    &__nav{
      margin-top: 1em;
    }
  }
}
