.order{
  @media screen and (min-width: 993px){
    .cart__grid{
      grid-template-columns: calc(100% - 24.1875em - 1.875em) 24.1875em;
    }
  }
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 24.1875em - 1.875em) 24.1875em;
    gap: 1.25em 1.875em;
    align-items: center;
  }
}

.order-guide{
  display: flex;
  align-items: center;
  &__play{
    width: 4.3125em;
    height: 4.3125em;
    display: block;
    position: relative;
    margin-right: .6875em;
    flex-shrink: 0;
    &:before{
      content: '';
      @include centerAbsolute;
      background-image: url(../img/play2.svg);
      @include bgImgSize(100%);
      width: 3.3125em;
      height: 3.3125em;
      border-radius: 50%;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
}

.orders-list{
  border: $border;
  border-radius: .3125em;
  margin-top: .75em;
}

.cart-row2{
  .cart-row{
    &__img{
      align-self: flex-start;
    }
    &__inner{
      grid-template-columns: 6.8125em calc(100% - 6.8125em - 1.25em);
    }
    &__info{
      max-width: 18.75em;
    }
    &__art{
      margin-top: .125em;
    }
    &__sum{
      width: 7em;
    }
    &__count{
      right: 9.125em;
    }
    &__title{
      padding-right: 1em;
    }
  }
  .product-card__prices{
    margin-top: .375em;
  }
  .product-card__available{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15.875em;
    width: 9em;
  }
  .empty-message{
    margin-top: .625em;
    color: $sh;
    padding: .5em .625em;
  }
}

.buy-group{
  padding: 1.25em;
  border-radius: .3125em;
  border: $border;
  margin-top: 3.125em;
  &:not(._co){
    ._hide-on-personal{
      display: none;
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    font-weight: 500;
    color: #383838;
  }
  &__login{
    padding: 0 1.25em;
    height: 2.1875em;
    margin-left: .8125em;
  }
  &__hint{
    display: flex;
    align-items: center;
    margin-left: .8125em;
    color: $darkBlue;
    img{
      width: 1.5em;
      margin-right: .375em;
    }
  }
  .profile-form__type{
    margin-top: 2.25em;
  }
  &__fields{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.pay-way,
.pickup{
  margin-top: 1.875em;
  border-radius: .3125em;
  border: $border;
  padding: 1.25em;
  .warehouse{
    margin-top: 1.25em;
  }
}

.pickup-time{
  margin-top: 1.5em;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.6875em / 4));
    gap: 1.5625em;
    margin-top: 1em;
  }
}

.time-radio{
  min-height: 3.625em;
  display: flex;
  align-items: center;
  padding: .875em 1.125em;
  input:checked~.time-radio__radio{
    background-size: 100% auto;
    border: none;
    &:after{
      border-color: $blue2;
    }
  }
  &__radio{
    margin-top: -1em;
    border: 1px solid #D6D6D6;
    width: 1.3125em;
    height: 1.3125em;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: .9375em;
    background-image: url(../img/radio.svg);
    @include bgImgSize(0);
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      border-radius: .3125em;
      width: 100%;
      height: 100%;
      border: $border3;
    }
  }
  &__img{
    margin-left: auto;
    flex-shrink: 0;
    width: 2em;
    &._big{
      width: 3.4375em;
    }
  }
}

.pay-way{
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 3.125em / 3));
    gap: 1.5625em;
    margin-top: 1.5em;
  }
  .time-radio__radio{
    margin-top: 0;
  }
}

._open-auth{
  .auth-modal{
    transform: translateX(0);
    &__wrap{
      opacity: 1;
      pointer-events: unset;
    }
    &__bg{
      display: block;
    }
    &__close{
      display: flex;
    }
  }
}

.auth-modal{
  @include menu;
  padding: 2.5em 1.875em;
  transform: translateX(120%);
  transform: translateX(0%);
  position: absolute;
  left: unset;
  right: 0;
  width: 20.625em;
  &__wrap{
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: $vh;
    z-index: 100;
    overflow: hidden;
  }
  &__close{
    top: 1em;
    right: 1em;
    left: unset;
    z-index: 22;
  }
  &__close,
  &__bg{
    display: none;
  }
  &__close{
    //opacity: 0.4;
    &:hover{
      //opacity: 1;
    }
  }
  &__title{
    text-align: center;
  }
  .login-form{
    margin-top: .94em;
    padding: 0;
    background: transparent;
  }
}

.order-thank{
  width: 65.125em;
  max-width: 100%;
  margin: 4.25em auto 0;
  &__top{
    padding: .75em .9375em;
    text-align: center;
    border-radius: .3125em;
    background: $gray;
    color: $darkBlue;
  }
}

.order-info{
  margin-top: 2em;
  border-radius: 0.3125em;
  border: 1px solid $gray;
  padding: 1.5em 2.5em;
  display: flex;
  justify-content: space-between;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      @media screen and (min-width: 993px){
        flex-grow: 1;
        &:after{
          content: '';
          height: 4.625em;
          width: 1px;
          border-right: 1px dashed $comm;
          min-height: 100%;
          flex-shrink: 0;
          margin: 0 auto;
        }
      }
    }
    &-inner{
      @include grid100gap(.125em);
    }
  }
  &__title{
    font-size: 1em;
    line-height: 144%;
    color: #4C4C4C;
  }
  &__desc{
    font-size: 1em;
    line-height: 144%;
    color: #414042;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .order{
    &__top{
      grid-template-columns: calc(100% - 17.1875em - 1.875em) 17.1875em;
    }
  }

  .orders-list{
    border: none;
    @include grid100gap(1.25em);
  }

  .cart-row2{
    .cart-row{
      &__inner{
        grid-template-columns: 3.875em calc(100% - .75em - 3.875em);
      }
      &__info{
        max-width: 100%;
      }
      &__art{
        display: flex;
        align-items: center;
      }
      &__bottom{
        margin-top: .5em;
      }
      &__remove{
        top: 0;
        left: unset;
        right: 0;
      }
    }
    .product-card__available{
      position: static;
      margin-left: .8125em;
      transform: unset;
      width: auto;
    }
    .empty-message{
      width: calc(100% + 4.5625em);
      margin: .625em 0 0 -4.5625em;
      align-items: center;
      padding: .625em .9375em;
    }
  }

  .buy-group{
    margin-top: 1.25em;
    padding: 1.5em 0 0;
    border: none;
    border-top: $border;
    border-radius: 0;
    &__hint{
      max-width: 22.5em;
    }
  }

  .pickup{
    border: none;
    padding: 0;
  }

  .pickup-time{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .time-radio{
    min-height: 3.4375em;
    br{
      display: none;
    }
    &__radio{
      margin-top: 0;
    }
  }

  .pay-way{
    padding: 1.25em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .order-thank{
    margin-top: 1em;
  }

  .order-info{
    border: none;
    padding: 0;
    margin: 2.25em 0;
  }
}


@media screen and (max-width: 576px){
  .order{
    &__top{
      grid-template-columns: 100%;
    }
  }

  .cart-row2{
    .empty-message{
      align-items: flex-start;
    }
  }

  .buy-group{
    &__top{
      flex-wrap: wrap;
    }
    &__hint{
      width: 100%;
      margin: .5em 0 0;
    }
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .pickup-time{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .pay-way{
    &__items{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .order-thank{
    margin-top: .5em;
    &__top{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
  }

  .order-info{
    @include grid100gap(1.5em);
  }
}
