.catalog{
  &__grid{
    display: grid;
    grid-template-columns: 15.5em calc(100% - 15.5em - 1.875em);
    gap: 1.875em;
  }
  &__info{
    display: flex;
    margin-top: 1.25em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__title{
    font-size: 2.3125em;
    line-height: 146%;
    font-weight: 600;
    font-family: $font2;
  }
  &__count{
    color: $p;
    padding: .25em 0 0 .625em;
  }
  .search-field{
    margin-top: 1.25em;
  }
}

.catalog-menu{
  &__top,
  &__bottom,
  &__bg{
    display: none;
  }
}

.category-menu{
  padding: 1.5em 1.25em;
  border: 1px solid #eff1f4;
  border-radius: .25em;
  &__title{
    ._view2{
      display: none;
    }
  }
  &__top{
    display: flex;
    margin-top: 1.5em;
  }
  &__back{
    display: flex;
    align-items: center;
    font-weight: 500;
    svg{
      width: .75em;
      margin-right: .5em;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__bottom{
    margin-top: .75em;
    padding-left: 1.25em;
  }
  .menu{
    @include grid100gap(.5em);
    a{
      font-size: 1em;
      line-height: 144%;
    }
  }
  .menu-item-has-children{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    &:after{
      content: '';
      width: .75em;
      height: .75em;
      flex-shrink: 0;
      background-image: url(../img/arrow3.svg);
      @include bgImgSize(100%);
      cursor: pointer;
      position: absolute;
      top: .3125em;
      right: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &._active{
      &:after{
        transform: rotate(-180deg);
      }
    }
    &>{
      a{
        cursor: pointer;
        width: 100%;
        padding-right: 1em;
      }
    }
    ul{
      display: none;
      width: 100%;
      order: 3;
      padding-left: 1em;
      padding-top: .375em;
      display: none;
      li{
        @include lastM(0 0 .375em);
      }
      a{
        font-size: .875em;
        color: $p;
        &:hover{
          color: $blue2;
        }
      }
      .current-menu-item{
        a{
          color: $blue2;
        }
      }
    }
  }
  &__more{
    display: none;
  }
}

.filter-btn{
  display: none;
}

.filter{
  padding: 1.5em 1.25em;
  border: 1px solid #eff1f4;
  border-radius: .25em;
  margin-top: 1.25em;
  &__items{
    @include grid100gap(1.5em);
    margin-top: 1.5em;
  }
}

.filter-group{
  &._active{
    .filter-group__btn{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    font-size: 1em;
    line-height: 144%;
  }
  &__btn{
    width: 1em;
    @include center;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: .75em;
    display: none;
  }
  &__checks{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .m-check{
      @include lastM(0 0 .75em);
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__info{
      margin-top: 1em;
    }
    &__title{
      font-size: 1.75em;
    }
    &__count{
      font-size: .75em;
      padding: .25em 0 0 .625em;
    }
  }

  .filter-btn{
    display: flex;
    width: 100%;
    margin-top: .9375em;
  }

  ._open-filter{
    .catalog-menu__bg{
      display: block;
    }
    .catalog-menu{
      transform: translateX(0);
    }
  }

  .catalog-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    max-width: 22.5em;
    background-color: #fff;
    z-index: 30;
    transform: translateX(-120%);
    transition: all .3s;
    &__bg{
      z-index: 29;
    }
    &__top{
      border-bottom: $border2;
      height: 3.75em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 1.25em;
    }
    &__title{
      font-size: 1.25em;
      font-weight: 500;
    }
    &__back,
    &__close{
      width: 1.25em;
      @include center;
      img{
        width: 100%;
        display: block;
      }
    }
    &__bottom{
      @include grid100gap(.625em);
      padding: .9375em 1.25em;
      border-top: $border3;
      background-color: $gray;
    }
    &__btn{
      height: 2.9375em;
    }
    &__scroll{
      height: calc(100% - 3.75em - 8.4375em);
      overflow-y: auto;
      padding: 1.25em;
    }
  }

  .category-menu{
    padding: .75em .9375em;
    &:not(._show){
      .menu{
        &>.menu-item:nth-child(3)~.menu-item{
          display: none;
        }
      }
    }
    &._show{
      .category-menu__more{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &__top{
      margin-top: .75em;
    }
    &__title{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__bottom{
      margin-top: .375em;
    }
    .menu{
      gap: .25em;
    }
    &__more{
      display: flex;
      margin-top: .25em;
      font-size: 1em;
      line-height: 144%;
      text-decoration: underline;
      color: $blue2;
      ._view2{
        display: none;
      }
    }
  }

  .filter{
    border: none;
    padding: 0;
    margin-top: 2.5em;
    &__title{
      display: none;
    }
  }
}


