.compare{
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.75em;
  }
  &__grid{
    margin-top: 2.5em;
    position: relative;
  }
}

.compare-sort{
  display: flex;
  align-items: center;
  &__select{
    margin-left: .5em;
    min-width: 17.625em;
  }
}

.compare-slider{
  .swiper{
    &-slide{
      width: 20%;
      padding-right: 0;
    }
    &-button{
      top: 8em;
      &-prev{
        left: calc(20% - 1.5em + 1.875em);
        margin-left: -1.5625em;
        z-index: 5;
      }
      &.swiper-button-disabled{
        opacity: 0;
      }
    }
    &-scrollbar{
      z-index: 5;
      margin-top: 2.5em;
      height: .25em;
      background-color: $gray;
      &-drag{
        background-color: $black;
      }
    }
  }
}

.compare-card{
  &__top{
    padding-right: 1.875em;
    height: 25.5em;
    border-bottom: $border3;
  }
}

.compare-group{
  margin-top: 2.5em;
  padding-top: 1.875em;
  position: relative;
  &__top{
    position: absolute;
    top: 0;
    left: 0;
  }
  &__row{
    display: flex;
    align-items: center;
    height: 2.8125em;
    &:nth-child(odd){
      background-color: #FAFAFA;
    }
  }
}

.compare-sidebar{
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
  height: 100%;
  z-index: 3;
  width: calc(20% - 1.5em + 1.875em);
  &__top{
    height: 25.5em;
    border-bottom: $border3;
    position: relative;
  }
  .delete-btn{
    position: absolute;
    left: 0;
    bottom: 1.25em;
  }
  .compare-group{
    &__row{
      padding-left: .625em;
      font-weight: 700;
    }
  }
  &__radios{
    @include grid100gap(.75em);
    margin-top: .75em;
  }
}

.compare-type{
  display: none;
}


@media screen and (max-width: 992px){
  .compare{
    &__nav{
      margin-top: 1em;
    }
    &__grid{
      margin-top: 1.5em;
    }
  }

  .compare-sort{
    &__title{
      display: none;
    }
    &__select{
      margin: 0;
    }
  }

  .compare-slider{
    .swiper{
      &-slide{
        width: 25%;
        &-hide-mob{
          display: none;
        }
      }
      &-button{
        display: none;
      }
    }
  }

  .compare-card{
    &__top{
      padding-right: .93875em;
      height: 25em;
      border-bottom: none;
    }
  }

  .compare-group{
    margin-top: 1.25em;
    &:first-child{
      margin-top: 0;
    }
    &__row{
      align-items: flex-end;
      padding: .375em .9375em;
      height: 3.375em;
    }
  }

  .compare-sidebar{
    width: 100%;
    background-color: transparent;
    pointer-events: none;
    &__top{
      opacity: 0;
      height: 25em;
    }
    .delete-btn{
      display: none;
    }
    .compare-group__row{
      align-items: flex-start;
      background-color: transparent;
    }
  }

  .compare-type{
    @include grid100gap(.5em);
    position: absolute;
    top: 19.25em;
    left: 0;
    &__field{
      display: flex;
      align-items: center;
    }
    .m-switch{
      margin-right: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .compare{
    &__nav{
      margin-top: .75em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__grid{
      margin-top: 1.25em;
    }
  }

  .compare-sort{
    width: 100%;
    order: 2;
    margin: 1.25em 0 0;
    &__select{
      width: 100%;
    }
  }

  .compare-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}
