.m-section{
  padding: 2.5em 0;
}

.products{
  padding: 2.5em 0;
  &._last{
    padding: 0 0 5em;
  }
  &--sales{
    padding: 5em 0 6.25em;
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    ._view2{
      display: none;
    }
  }
}

.collection-slider{
  margin-top: 1.25em;
  .swiper{
    &-slide{
      width: 20%;
    }
    &-button{
      top: 8em;
      @media screen and (min-width: 993px){
        &.swiper-button-disabled{
          opacity: 0;
        }
      }
    }
  }
}

.product-card{
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  &:hover{
    .product-card__img:after{
      opacity: 1;
    }
    .product-card__detail-btn{
      opacity: 1;
    }
  }
  &__top{
    position: relative;
    margin-bottom: 1em;
  }
  &__detail-btn{
    @include centerAbsolute;
    z-index: 3;
    height: 2.375em;
    padding: 0 .625em;
    font-weight: 400;
    white-space: nowrap;
    opacity: 0;
    svg{
      width: 1.1875em;
      margin-right: .3125em;
    }
    span{
      font-size: .875em;
    }
  }
  &__action{
    z-index: 2;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0 .25em 0 .625em;
    transition: all .3s;
    width: 3.125em;
    height: 5em;
    @include center;
    flex-direction: column;
    background-color: $gray;
  }
  .favourite-btn{
    margin-bottom: .625em;
  }
  &__img{
    border-radius: .25em;
    @include imgCover(103%);
    &:after{
      transition: all .3s;
      opacity: 0;
    }
  }
  &__badges{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    left: .625em;
    bottom: .625em;
    .m-badge{
      @include lastM(0 0 .3125em);
    }
  }
  &__available{
    font-weight: 500;
    img{
      width: .875em;
      margin-right: .25em;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 144%;
    @include truncate(2);
    margin-top: .75em;
  }
  &__prices{
    margin-top: .8125em;
    display: flex;
    align-items: center;
    .m-badge{
      margin-left: .5em;
    }
  }
  &__price{
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 500;
    margin-right: .5em;
  }
  &__price2{
    font-size: .875em;
    color: #73757b;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 120%;
      height: 1px;
      background-color: #73757b;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%) rotate(-10deg);
    }
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: .875em;
  }
  .cartcalc{
    margin-right: .75em;
  }
  &__btn{
    padding: 0;
    flex-grow: 1;
  }
}

.more-products{
  background-image: url(../img/product-bg.webp);
  background-size: cover;
  height: 100%;
  border-radius: .375em;
  padding: 1em;
  @include center;
  text-align: center;
  flex-direction: column;
  color: $white;
  .m-badge{
    margin-bottom: .9375em;
  }
  &__desc{
    margin-top: .9375em;
    opacity: 0.7;
  }
  &__link{
    margin-top: .9375em;
    height: 2.1875em;
    padding: 0 2em;
  }
}

._flex{
  display: flex;
  align-items: center;
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.25em 0;
  }

  .products,
  .m-section{
    &__inner{
      position: relative;
    }
    &__top{
      min-height: 2.3125em;
      display: flex;
      align-items: center;
    }
    .swiper-nav{
      position: absolute;
      right: 0;
      top: -.4375em;
      display: flex;
    }
    .swiper-button{
      position: static;
      transform: unset;
      border: $border;
      margin: 0 0 0 .625em;
    }
  }

  .m-section{
    &__top{
      min-height: 3.125em;
    }
    .swiper-nav{
      top: 0;
    }
  }

  .products{
    padding: 1.25em 0;
    &._last{
      padding: 0 0 2.5em;
    }
    &--sales{
      padding: 2.5em 0;
    }
    &__nav{
      display: block;
      margin-top: 1.25em;
    }
    &__link{
      position: absolute;
      top: 0;
      right: 8.75em;
      height: 2.3125em;
    }
    .collection-slider{
      position: static;
    }
  }

  .collection-slider{
    .swiper{
      &-slide{
        width: 25%;
      }
    }
  }

  .product-card{
    &__img{
      padding-bottom: 97%;
    }
    &__action{
      width: 2.25em;
      height: 3.5625em;
    }
    .favourite-btn{
      margin-bottom: .3125em;
    }
    &__badges{
      left: .3125em;
      bottom: .3125em;
      .m-badge{
        @include lastM(0 0 .1875em);
      }
    }
    .m-badge{
      height: 1em;
      padding: 0 .3125em;
      span{
        font-size: .6875em;
      }
    }
    &__price{
      font-size: 1.0625em;
    }
    &__price2{
      font-size: .8125em;
    }
    .cartcalc{
      width: 6.5em;
      margin-right: .5em;
    }
    &__btn{
      &._cart{
        background-image: url(../img/cart.svg);
        @include bgImgSize(1.1875em);
        span{
          display: none;
        }
      }
    }
  }

  .more-products{
    padding: .9375em;
  }
}


@media screen and (max-width: 576px){
  .products{
    &__inner{
      padding-bottom: 5.625em;
    }
    &__top{
      min-height: unset;
    }
    &__nav{
      margin-top: .75em;
    }
    &__link{
      width: auto;
      padding: 0 1em;
      top: unset;
      right: 50%;
      bottom: 1em;
      margin-right: .4375em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .swiper-nav{
      right: 1.5em;
      bottom: .625em;
      top: unset;
    }
  }

  .products2{
    .swiper-nav{
      right: 50%;
      transform: translateX(50%);
    }
    .swiper-button{
      margin: 0 .3125em;
    }
  }

  .collection-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .m-section{
    &__inner{
      padding-bottom: 4.5em;
    }
    &__top{
      min-height: unset;
    }
    .swiper-nav{
      right: 50%;
      bottom: 0;
      top: unset;
      transform: translateX(50%);
    }
    .swiper-button{
      margin: 0 .3125em;
    }
  }
}
