.faq-block{
  padding: 5em 0;
  background-color: $gray;
  &__groups{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
    gap: 3.125em;
    margin-top: 1.875em;
  }
  .faq-group{
    margin: 0 !important;
  }
  .qa-card{
    &__show,
    &__hidden{
      border-color: $comm;
    }
  }
}

.find{
  padding: 5em 0;
  &__top{
    display: grid;
    grid-template-columns: 44.5em 30.3125em;
    gap: .75em 3.125em;
    align-items: center;
  }
  .categories-slider{
    .swiper-slide{
      width: 20%;
    }
    @media screen and (min-width: 993px){
      .swiper-button{
        &.swiper-button-disabled{
          opacity: 0;
        }
      }
    }
  }
  .category-card{
    border: $border2;
    outline: 1px solid transparent !important;
    box-shadow: unset;
    &:hover{
      border-color: $blue2;
      outline-color: $blue2 !important;
    }
  }
}

.advantages{
  padding: 5em 0;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 15.625em / 3));
    gap: 3em 7.8125em;
  }
}

.start-save{
  background-color: $blue2;
  display: flex;
  align-items: center;
  padding: 1.25em;
  border-radius: .3125em;
  color: #fff;
  &__img{
    width: 1.9375em;
    margin-right: .875em;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 600;
    flex-grow: 1;
  }
  &__link{
    flex-shrink: 0;
    margin-left: .875em;
  }
}

.cooperate{
  min-height: 25.1875em;
  position: relative;
  background: $darkBlue;
  color: #fff;
  padding: 2.5em 0;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  .container{
    position: relative;
    z-index: 2;
  }
  .breadcrumbs {
    li {
      color: rgba(#fff, .5);
      &:last-child {
        a {
          color: #fff;
        }
      }
    }
  }
  &__content{
    max-width: 37.25em;
  }
  &__desc{
    margin-top: 1.5em;
    color: $gray;
    max-width: 32.5em;
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
  }
  &__btn{
    padding: 0 1.5625em;
  }
}


@media screen and (max-width: 992px){
  .faq-block{
    padding: 2.5em 0;
    &__groups{
      grid-template-columns: 100%;
    }
  }

  .find{
    padding: 2.5em 0;
    &__top{
      grid-template-columns: 100%;
      max-width: 28.4375em;
    }
    .main__items{
      margin-top: 1.5em;
    }
    .categories-slider{
      position: relative;
      .swiper{
        &-slide{
          width: 25%;
        }
        &-nav{
          position: absolute;
          bottom: 100%;
          right: 0;
          margin-bottom: 2.5em;
          display: flex;
        }
        &-button{
          position: static;
          margin: 0 0 0 .625em;
          transform: unset;
        }
      }
    }
  }

  .advantages{
    padding: 2.5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
    }
  }

  .cooperate{
    min-height: 20em;
    padding-top: 1.25em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .breadcrumbs{
      margin-bottom: 1.25em;
    }
    &__content{
      max-width: 22.0625em;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      height: 3.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .find{
    .categories-slider{
      .swiper{
        &-container{
          width: 100%;
        }
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(2,calc(50% - .4375em));
          gap: .625em .875em;
          transform: unset !important;
        }
        &-slide{
          width: 100%;
          padding: 0;
        }
        &-nav{
          display: none;
        }
      }
    }
  }

  .advantages{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .cooperate{
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
  }
}
