.catalog-item{
  padding-bottom: 2.5em;
  &__inner{
    position: relative;
  }
  &__sidebar{
    position: absolute;
    width: 24.1875em;
    height: 100%;
    right: 0;
    top: 0;
  }
  &__sticky{
    position: sticky;
    top: 1em;
  }
  &__top,
  &__bottom{
    display: grid;
    grid-template-columns: calc(100% - 24.1875em - 1.875em) 24.1875em;
    gap: 1.875em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 32.875em calc(100% - 32.875em - 1.25em);
    gap: 1.25em;
  }
  &__bottom{
    margin-top: 3.75em;
  }
}

.gallery{
  position: relative;
  .product-card__badges{
    z-index: 3;
    top: 1.25em;
    right: 1.25em;
    bottom: unset;
    left: unset;
    align-items: flex-end;
  }
  .m-badge{
    height: 1.625em;
    padding: 0 .75em;
  }
  &__big{
    .swiper{
      overflow: unset;
      &-slide{
        opacity: 0;
        pointer-events: none;
        &-active{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .gallery__img{
      background-image: url(../img/gallery-bg.svg);
      background-size: cover;
      img{
        width: 100% !important;
        height: 100% !important;
      }
    }
  }
  &__img{
    background-color: $gray;
    display: block;
    @include imgCover(100%);
  }
  &__small{
    overflow: hidden;
    margin-top: .625em;
    position: relative;
    .gallery__img{
      border: 1px solid $gray;
      cursor: pointer;
      transition: all .3s;
    }
    .swiper{
      width: calc(100% - 4.375em);
      margin: 0;
      overflow: unset;
      &-slide{
        padding-right: .375em;
        width: calc(100% / 6);
        &-thumb-active{
          .gallery__img{
            border-color: $blue2;
          }
        }
        @media screen and (min-width: 993px){
          &-next+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide{
            pointer-events: none;
            img{
              opacity: .12;
            }
          }
        }
      }
    }
  }
  .swiper-button{
    right: .625em;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 0;
    &:not(:hover){
      background-color: transparent;
      box-shadow: none;
    }
  }
}

.js-image-zoom__zoomed-image{
  background-color: $gray !important;
  width: 32.875em !important;
  height: 32.875em !important;
}

.product-info{
  margin-top: -.5em;
  &__articul{
    margin-top: .5em;
    color: $p;
    span{
      font-weight: 500;
      color: $sh;
    }
  }
}

.product-info2{
  margin-top: 1.25em;
  &__desc{
    margin-top: .75em;
    color: $p;
    @include truncate(2);
  }
  &__nav{
    margin-top: .375em;
    display: flex;
    color: $sh;
    font-weight: 500;
  }
}

.product-props{
  padding-top: 1.25em;
  margin-top: 1.625em;
  border-top: $border;
  &__items{
    margin-top: .75em;
    @include grid100gap(.25em);
  }
  .prop-item{
    &:after{
      align-self: flex-end;
      transform: translateY(-.375em);
    }
    &__title,
    &__desc{
      line-height: 144%;
    }
  }
  &__hint{
    display: flex;
    align-items: flex-start;
    margin-top: .75em;
    img{
      width: 1em;
      margin-right: .25em;
    }
  }
}

.product-cart,
.product-pickup{
  padding: 1.875em;
  box-shadow: 0 .375em 3.125em rgba(129, 151, 208, 0.11), 0 .125em .375em rgba(50, 75, 140, 0.06);
  border-radius: .75em;
}

.product-cart{
  .product-card__prices{
    margin-top: 0;
  }
  .product-card__price{
    font-size: 2.375em;
    font-weight: 600;
    margin: 0;
  }
  .product-card__price2{
    font-size: 1em;
    order: 2;
  }
  .product-card__sale{
    margin: 0 .75em;
    height: 1.625em;
    padding: 0 .75em;
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__btn{
    height: 3.125em;
    padding: 0 2.5625em;
    margin-left: .75em;
    &._big{
      margin: 0;
      width: 100%;
    }
  }
  .cartcalc{
    height: 3.125em;
    border: $border;
    background-color: transparent;
    width: 9.5em;
    grid-template-columns: 3.125em calc(100% - 6.25em) 3.125em;
  }
  &__nav2{
    display: flex;
    align-items: center;
    margin-top: 1.125em;
  }
  .compare-btn,
  .favourite-btn{
    width: auto;
    color: $p;
    svg{
      width: 1.25em;
      margin-right: .5em;
    }
  }
  .compare-btn{
    margin-right: 1.875em;
  }
  &__from{
    align-self: flex-end;
    padding: 0 .625em .75em 0;
  }
  &__hint{
    color: #727378;
    margin-top: .5em;
  }
  .empty-message{
    padding: .75em .9375em;
    margin-top: .75em;
    &._orange{
      align-items: flex-start;
      border-color: $sh;
      background-color: #FAF8F7;
      b{
        color: $sh;
      }
    }
  }
}

.product-pickup{
  margin-top: 1.875em;
  .product-card__available{
    order: 2;
  }
  &__props{
    margin-top: 1em;
  }
  &__address{
    margin-top: .5em;
    display: flex;
    align-items: center;
    color: $blue2;
    font-weight: 500;
    img{
      width: 1.25em;
      margin-right: .375em;
    }
  }
  &__map{
    margin-top: 1.25em;
    border-radius: .375em;
    @include imgCover(64.2%);
    padding-bottom: 0;
    height: 13.125em;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__top,
    &__bottom{
      grid-template-columns: 100%;
    }
    &__grid{
      grid-template-columns: 20.625em calc(100% - 20.625em - 1.5625em);
      gap: 1.5625em;
    }
    &__sidebar{
      position: static;
      width: 100%;
    }
    &__sticky{
      position: static;
    }
    &__bottom{
      margin-top: 1.875em;
    }
  }

  .gallery{
    overflow: hidden;
    .product-card__badges{
      top: .625em;
      right: .625em;
    }
    &__small{
      .swiper{
        width: calc(100% - 5em);
        &-slide{
          width: calc(100% / 3);
          &-next+.swiper-slide+.swiper-slide{
            pointer-events: none;
            img{
              opacity: .12;
            }
          }
        }
      }
    }
    .swiper-button{
      right: 1em;
    }
  }

  .product-info{
    padding: 1.25em 0;
  }

  .product-props{
    @media screen and (min-width: 577px){
      padding-top: 0;
      border: none;
    }
  }

  .product-cart{
    padding: 1.25em;
    .product-card__prices{
      margin-top: .25em;
    }
    .product-card__price{
      font-size: 1.5em;
    }
    &__nav{
      margin-top: .75em;
    }
    &__btn{
      padding: 0;
      flex-grow: 1;
    }
    &__nav2{
      margin-top: 1em;
    }
  }

  .product-pickup{
    margin-top: 0;
    padding: 1.25em;
  }

  .js-image-zoom__zoomed-area,
  .js-image-zoom__zoomed-image{
    display: none !important;
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__bottom{
      margin-top: 1.25em;
    }
  }

  .product-info{
    padding: 0;
  }

  .product-cart{
    margin-top: .875em;
  }
}
