.cart{
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 1.875em;
  }
  &__all{
    margin-right: 2.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 18.75em - 1.875em) 18.75em;
    gap: 1.5625em 1.875em;
    margin-top: 1.25em;
  }
  &__items{
    @include grid100gap(1.25em);
  }
}

.cart-sum{
  position: sticky;
  top: 1em;
  padding: 1.875em;
  border-radius: 0.3125em;
  box-shadow: 0 .375em 3.125em 0 rgba(129, 151, 208, 0.11), 0 2px 6px 0 rgba(50, 75, 140, 0.06);
  @include grid100gap(1.25em);
  &__props{
    @include grid100gap(.9375em);
  }
  &__btn{
    width: 100%;
    height: 3.125em;
    padding: 0;
  }
  &__hint{
    border-radius: 0.1875em;
    border: 1px dashed #7EACB8;
    background: #F6FBFC;
    padding: .625em;
    color: $grayText;
  }
  &__nav{
    @include grid100gap(.625em);
  }
  &__info{
    &-item{
      padding: .625em 0;
      @include grid100gap(.19em);
      border-bottom: $border2;
    }
    &-title{
      font-size: 1em;
      line-height: 169%;
      font-weight: 500;
    }
    &-desc{
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    &:not(.p-text){
      color: #2C333D;
    }
    &._big{
      font-weight: 500;
      font-size: 1.125em;
      line-height: 132%;
    }
  }
  &__desc{
    order: 2;
    &._big{
      font-size: 1.25em;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
}

.promo-code{
  @include grid100gap(.375em);
  .text15{
    line-height: 140%;
  }
  .fg{
    input{
      height: 3.1875em;
    }
  }
  &__btn{
    height: 2.1875em;
    border-width: 2px;
  }
  &__message{
    border-radius: 0.25em;
    background: rgba(8, 165, 108, 0.06);
    height: 2.1875em;
    @include center;
    color: #08A56C;
    font-weight: 500;
    img{
      margin-right: .625em;
      width: 1em;
    }
  }
  &._error,
  &._error2{
    .fg{
      input{
        border-color: $red;
        color: $red;
      }
    }
  }
  &._error{
    .promo-code__message{
      color: $red;
      background: rgba($red,.07);
      img{
        width: 1.5em;
      }
    }
  }
  &._error2{
    .promo-code__message{
      color: $p;
      border: 2px solid $p;
      background: transparent;
    }
  }
}

.cart-row{
  padding: 1.25em;
  position: relative;
  ._border{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: $border;
    border-radius: .3125em;
    pointer-events: none;
  }
  &__inner{
    display: grid;
    grid-template-columns: 1.25em 6.8125em calc(100% - 6.8125em - 3.75em);
    gap: 1.25em;
    align-items: center;
  }
  &__img{
    @include imgCover(100%);
    background-color: $gray;
  }
  &__check{
    @media screen and (min-width: 993px){
      position: static;
      width: 1.25em;
      height: 1.25em;
      input{
        top: unset;
        left: unset;
        width: 1.25em;
        height: 1.25em;
        &:checked~._border{
          border-color: $blue;
        }
      }
      label{
        padding: 0;
      }
    }
  }
  &__content{
    position: relative;
  }
  &__info{
    max-width: 27.5em;
  }
  &__title{
    @include truncate(2);
  }
  &__art{
    margin-top: .5em;
  }
  &__count,
  &__remove,
  &__sum{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &__count{
    right: 14.375em;
  }
  .cartcalc{
    width: 6.5em;
    grid-template-columns: 2.125em calc(100% - 2.125em * 2) 2.125em;
  }
  &__sum{
    width: 11.125em;
    right: 0;
  }
  &__remove{
    right: 0;
    width: 1em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
  .empty-message{
    min-height: unset;
    padding: .625em .9375em;
    justify-content: unset;
    align-items: flex-start;
    margin-top: 1.25em;
    border-color: $sh;
    background-color: #FAF8F7;
    b{
      color: $sh;
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .cart-row{
    padding: 0;
    ._border{
      display: none;
    }
    &__inner{
      align-items: flex-start;
      grid-template-columns: 1.25em 3.875em calc(100% - 2.75em - 3.875em);
      gap: .75em;
    }
    &__sum{
      display: none;
    }
    &__bottom{
      display: flex;
      align-items: center;
      margin-top: .75em;
    }
    .product-card__prices{
      margin-top: 0;
    }
    &__count{
      position: static;
      transform: unset;
      margin-left: .875em;
    }
    .cartcalc{
      height: 2.25em;
    }
    &__remove{
      top: 0;
      transform: unset;
    }
    .empty-message{
      margin-top: .625em;
    }
  }

  .cart-sum{
    padding: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__grid{
      margin-top: 1.5em;
      gap: 1.875em;
    }
    &__nav{
      margin-top: .25em;
    }
  }

  .cart-row{
    &__content{
      position: static;
    }
    &__bottom{
      display: block;
    }
    &__remove{
      left: .125em;
      top: 2.5em;
      right: unset;
    }
    &__count{
      margin: .375em 0 0;
    }
  }
}
