.page-top{
    padding: 2.5em 0 2em;
}

.breadcrumbs {
    margin-bottom: 1.5em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: rgba($black,.5);
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: -0em;
                right: 100%;
                margin-right: .325em;
                font-size: .875em;
                line-height: 130%;
                font-weight: 500;
            }
        }
        &:last-child{
            a{
                color: $black;
            }
        }
    }
    a{
        font-size: .875em;
        line-height: 130%;
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding: 1.5em 0 1em;
    }

    .breadcrumbs{
        margin-bottom: 1em;
    }
}


@media screen and (max-width: 576px){
    .page-top{
        padding-bottom: 2em;
        &._scroll{
            padding-bottom: 1em;
            .container{
                white-space: nowrap;
                overflow-x: auto;
                padding-bottom: 1em;
            }
        }
    }
}
